import React, { useEffect, useState } from "react";
import { Col, Container, Row, Dropdown } from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFilter } from '@fortawesome/free-solid-svg-icons';
import Button from "@restart/ui/esm/Button";
import DiscoverProjectCard from "../DiscoverProjectCard/DiscoverProjectCard";
import { connect } from "react-redux";
import { getProjects, beforeProject } from '../ArtistInterface/Create_project_modal/project.action'
import moment from 'moment';
import DateRangePicker from 'react-bootstrap-daterangepicker';
// you will need the css that comes with bootstrap@3. if you are using
// a tool like webpack, you can do the following:
import 'bootstrap/dist/css/bootstrap.css';
// you will also need the css that comes with bootstrap-daterangepicker
import 'bootstrap-daterangepicker/daterangepicker.css';
import { getUsers, beforeUser } from '../../components/user/user.action'
import Select from 'react-select'
import FullPageLoader from "../FullPageLoader/FullPageLoader";

const DiscoverProject = (props) => {
    const [projects, setProjects] = useState();
    const [pagination, setPagination] = useState();
    const [filterData, setFilterData] = useState({ name: '', username: '', startDate: '', endDate: '', creatorId: '' })
    const [searchQuery, setSearchQuery] = useState('')
    const [showResetBtn, setShowResetBtn] = useState(false);
    const [creators, setCreators] = useState([]);
    const [selectedCreator, setSelectedCreator] = useState({ label: "Select Creator", value: "" });
    const [fullLoader, setFullLoader] = useState(false)
    const [dispalyFilters, setDisplayFilters] = useState(false)

    useEffect(async () => {
        props.getProjects()
        props.getUsers('project-creators')
    }, [])

    useEffect(() => {
        if (props.projects.getProjectsAuth) {
            const { projects, pagination } = props.projects;
            setProjects(projects);
            setPagination(pagination);
            props.beforeProject();
        }
    }, [props.projects.getProjectsAuth])


    useEffect(() => {
        if (props.users.getUsersAuth) {
            let userOptions = []
            props.users.users.map((creator) => userOptions.push({ value: creator._id, label: creator.username }));
            setCreators(userOptions)
            // props.beforeUser()
        }
    }, [props.users.getUsersAuth])

    useEffect(() => {
        if (projects && creators)
            setFullLoader(false);
    }, [projects, creators])

    useEffect(() => {
        if (props.error)
            setFullLoader(false)
    }, [props.error])

    const handleLoadMore = () => {
        let qs = `page=${pagination?.page + 1}&limit${9}` + searchQuery;
        props.getProjects(qs)
    }

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        let data = filterData
        setFilterData({ ...data, [name]: value })
    }

    const handleReactSelect = (selectedOption, e) => {
        const { name } = e;
        setFilterData({ ...filterData, [name]: selectedOption ? selectedOption.value : "" });
        if (name === "creatorId")
            setSelectedCreator(selectedOption ? selectedOption : { label: "Select Creator", value: "" })
    }

    const handleSearch = async () => {
        let qs = ''
        for (const key in filterData) {
            setShowResetBtn(true)
            if (filterData[key] !== "")
                qs += `${key}=${filterData[key]}&`
        }
        setSearchQuery(qs);
        setFullLoader(true);
        props.getProjects(qs)
    }

    const handleReset = async () => {
        setFullLoader(true);
        setFilterData({ name: '', username: '', startDate: '', endDate: '', creatorId: '' })
        setSearchQuery('')
        setShowResetBtn(false)
        setSelectedCreator({ label: "Select Creator", value: "" });
        props.getProjects()
    }

    const handleEvent = (event, picker) => {
        let data = filterData;
        filterData.startDate = moment(picker.startDate).format('YYYY/MM/DD');
        filterData.endDate = moment(picker.endDate).format('YYYY/MM/DD')
        setFilterData({ ...data })
    }
    return (
        <>
            {fullLoader ? <FullPageLoader /> :
                <div className="discover-music">
                    <Container>
                        <div className="filters-section-1 mb-4" style={{ backgroundColor: dispalyFilters ? "#DE05BA" : '' }} onClick={() => setDisplayFilters(!dispalyFilters)}>
                            <Row>
                                <Col className="text-center">
                                    <span className="filter-icon-div">
                                        <FontAwesomeIcon icon={faFilter} className="filter-icon" style={{ color: dispalyFilters ? "#9000F3" : "#fff" }} />
                                    </span>
                                </Col>
                            </Row>
                        </div>
                        <div style={{ display: dispalyFilters ? 'block' : 'none' }} className="filters-section">
                            <div className="filter-body">
                                <Row>
                                    <Col md={12} className="">
                                        <div className="input-group header-search" >
                                            <Select className="react-select-filter" name="creatorId" value={selectedCreator} isClearable={selectedCreator.value !== "" ? true : false} options={creators} placeholder="Select Creator" onChange={(selectedOption, e) => handleReactSelect(selectedOption, e)} />
                                        </div>
                                    </Col>
                                    <Col md={12} className="mt-3">
                                        <div className="discover-music-banner d-flex">
                                            <div className="search-input-group">
                                                <input type="text" name="name" className="form-control" value={filterData.name} onChange={handleInputChange} placeholder="Project Name" />
                                            </div>
                                            <div className="search-input-group">
                                                <input type="text" name="username" className="form-control" value={filterData.username} onChange={handleInputChange} placeholder="Owner" />
                                            </div>
                                            <div className="date-picker search-input-group">
                                                <DateRangePicker
                                                    // initialSettings={state}
                                                    id="selectDate"
                                                    autoApply={false}
                                                    locale={{ format: "MMMM D, YYYY" }}
                                                    onEvent={handleEvent}
                                                // onCallback={handleCallback}
                                                >
                                                    <input type="text" className="form-control"
                                                    // onChange={handleDate}
                                                    />
                                                </DateRangePicker>
                                            </div>

                                        </div>
                                    </Col>
                                    <Col md={12} className="mt-3">
                                        <div className="discover-music-banner d-flex justify-content-start">
                                            {/* {showResetBtn && <div className="search-button">
                                                <Button className="btn reset-btn bg-gradient-theme" onClick={handleReset}>Reset</Button>
                                            </div>} */}
                                            <div className="search-button">
                                                <Button className="btn reset-btn bg-gradient-theme" onClick={handleReset}>Reset</Button>
                                            </div>
                                            <div className="search-button">
                                                <Button className="btn seacrh-btn" onClick={handleSearch}>Search</Button>
                                            </div>
                                        </div>
                                    </Col>
                                </Row>
                            </div>
                        </div>

                        {(projects && projects.length > 0) ?
                            <Row className="discover-music-list">
                                {projects?.map(project => {
                                    return (<Col lg={4} md={6} key={project._id}>

                                        <div className="discover-list">
                                            <DiscoverProjectCard project={project} />
                                        </div>
                                    </Col>)
                                })}
                            </Row> :
                            <Row className="mt-4">
                                <Col>
                                    <div className="home-slider-container"><span className="text-danger">No Item Found</span></div>
                                </Col>

                                {/* <div className="text-danger text-center padding-top fw-700">No Item Found</div> */}
                            </Row>}

                        {pagination?.page < pagination?.pages &&
                            <Row>
                                <Col md={12}>
                                    <div className="load-button text-center">
                                        <Button className="btn load-btn bg-gradient-theme" onClick={handleLoadMore}>Load More</Button>
                                    </div>
                                </Col>
                            </Row>}
                    </Container>
                </div>
            }
        </>
    );
};

const mapStateToProps = (state) => ({
    projects: state.projects,
    users: state.user

})


export default connect(mapStateToProps, { getProjects, beforeProject, getUsers, beforeUser })(DiscoverProject);