import React, { useState } from "react";
import music_img from '../../images/music_item.png';
import './Trending_slide.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlayCircle, faHeart, faPlay, faDotCircle } from '@fortawesome/free-solid-svg-icons';
import { Link, useHistory } from "react-router-dom";
import NftSlideModal from '../Nft_slide_modal/Nft_slide_modal.js';
import CartPopup from "../Cart/CartPopup";
import imagePlaceholder from "../../images/image-placeholder.jpg";
import BulkBuyPopUp from "../bulkBuy/bulkBuy";


function Trending_slide(props) {
    const [lgShow, setLgShow] = useState(false);
    const [smShows, setsmShows] = useState(false);
    const [bulkBuyShow, setBulkBuyShow] = useState(false);
    const hideModel = () => { setLgShow(false) };
    const hideModel1 = () => { setsmShows(false) };
    const hideModel2 = () => { setBulkBuyShow(false) };
    const history = useHistory()
    // const {name,description,image}=props?.nft;
    return (
        <>
            <div className="music-content d-flex align-items-center">
                <div className="music music-image">
                    <img
                        // onClick={() => setLgShow(true)}
                        onClick={() => { history.push(`song-detail/${props?.nft?._id}`) }}
                        className="img-fluid" src={props.nft?.image ? props.nft.image : imagePlaceholder} alt="missing" />
                </div>
                <div className="music-details">
                    <div className="music-head d-flex justify-content-between">
                        <div className="music-title">
                            <span onClick={() => { history.push(`song-detail/${props?.nft?._id}`) }}>{props.nft?.name}</span>
                            <div dangerouslySetInnerHTML={{ __html: props?.nft?.description }}></div>
                            <Link to={`/singer-details/${props.nft?.creator._id}`}>{props.nft?.creator.username}</Link>
                        </div>
                        <div className="songs-artist-button">
                            <button type="button" className="btn btn-theme ethereum-icon d-flex bg-gradient-theme" onClick={() => {
                                if (props.nft?.original)
                                    setBulkBuyShow(true)
                                else
                                    setsmShows(true);
                            }}>
                                <span className="nft-slide-price">{props?.nft?.currentPrice}</span> ETH
                            </button>
                        </div>
                    </div>
                    <div className="d-flex icons-row">
                        <div className="like">
                            <FontAwesomeIcon icon={faHeart} />
                            <span>{props.nft?.likes}</span>
                        </div>
                        <div className="view">
                            <FontAwesomeIcon icon={faPlay} />
                            <span>{props.nft?.timesPlayed}</span>
                        </div>
                        {props.nft?.track && <div className="subtract">
                            <span><FontAwesomeIcon icon={faDotCircle} /></span>
                            <span> {props.nft.track}/ {props.nft?.totalTracks}</span>
                        </div>}

                    </div>
                </div>

            </div>
            <div className="modal-wrapper">
                <NftSlideModal hideModel={hideModel} nft={props.nft} lgShow={lgShow} />
                <CartPopup nftType="trending" hideModel={hideModel1} smShows={smShows} setBulkBuyShow={setBulkBuyShow} nft={props.nft} />
                <BulkBuyPopUp hideModel={hideModel2} bulkBuyShow={bulkBuyShow} setsmShows={setsmShows} nft={props.nft} />
            </div>
        </>
    )
}

export default Trending_slide;