import { UPSERT_ALBUMS, BEFORE_ALBUMS, GET_ERRORS, GET_ALBUMS, GET_ALBUM, DELETE_ALBUM } from "../../redux/types";

const initialState = {
    album: null,
    albums: [],
    pagination: null,
    deleteNFTAuth: false,
    upsertNFTAuth: false,
    getNFTAuth: false,
    getAlbumsAuth: false
}

export default function (state = initialState, action) {
    switch (action.type) {
        case UPSERT_ALBUMS:
            return {
                ...state,
                album: action.payload,
                upsertAlbumAuth: true
            }
        case DELETE_ALBUM:
            return {
                ...state,
                album: action.payload,
                deleteAlbumAuth: true
            }
        case GET_ALBUMS:
            return {
                ...state,
                // albums: [...state.albums, ...action.payload.albums],
                albums: action.payload.albums,
                pagination: action.payload.pagination,
                getAlbumsAuth: true
            }
        case GET_ALBUM:
            return {
                ...state,
                album: action.payload,
                getAlbumAuth: true
            }
        case BEFORE_ALBUMS:
            return {
                ...state,
                album: null,
                albums: [],
                pagination: null,
                deleteAlbumAuth: false,
                upsertAlbumAuth: false,
                getAlbumAuth: false,
                getAlbumsAuth: false
            }
        default:
            return {
                ...state
            }
    }
}