import React, { useState } from "react";
import music_img from '../../../images/music_item.png';
// import './Trending_slide.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlayCircle, faHeart, faPlay, faDotCircle } from '@fortawesome/free-solid-svg-icons';
import { Link, useHistory } from "react-router-dom";
import NftSlideModal from '../Nft_slide_modal/Nft_slide_modal.js';
import CartPopup from "../Cart/CartPopup";


function SongsList(props) {
    const [lgShow, setLgShow] = useState(false);
    const [smShows, setsmShows] = useState(false);

    const hideModel = () => { setLgShow(false) };
    const hideModel1 = () => { setsmShows(false) };
    const history = useHistory()
    return (
        <>
            <div className="music-content-sp d-flex justify-content-between align-items-center">
                <div className="d-flex music-sp-wrapper">
                    <div className="music-sp music-img me-3" onClick={() => {
                        history.push(`/song-detail/${props?.nft?._id}`)
                    }}>
                        <img
                            //  onClick={() => setLgShow(true)}
                            className="img-fluid music-inner-img" src={props?.nft?.image} />
                    </div>
                    <div className="music-details-sp">
                        <div className="music-head d-flex">
                            <div className="music-title" onClick={() => {
                                history.push(`/song-detail/${props?.nft?._id}`)
                            }}>
                                <h3>
                                    {props.nft?.name}
                                </h3>
                                <span>{props.nft?.owner.username}</span>
                            </div>

                        </div>
                        <div className="d-flex icons-row">
                            <div className="like">
                                <FontAwesomeIcon icon={faHeart} />
                                <span>{props?.nft?.likes}</span>
                            </div>
                            <div className="view">
                                <FontAwesomeIcon icon={faPlay} />
                                <span>{props?.nft?.timesPlayed}</span>
                            </div>
                            <div className="subtract">
                                <span><FontAwesomeIcon icon={faDotCircle} /></span>
                                <span>{props?.nft?.track} / {props?.nft?.totalTracks}</span>
                            </div>

                        </div>
                    </div>
                </div>

                <Link type="button" to={`/song-detail/${props.nft?._id}`} className="btn btn-theme bg-gradient-green">
                    See details
                </Link>

            </div>
            <div className="modal-wrapper">
                <NftSlideModal hideModel={hideModel} lgShow={lgShow} />
                <CartPopup hideModel={hideModel1} smShows={smShows} nft={props.nft} />
            </div>
        </>
    )
}

export default SongsList;