import React, { useState, useEffect } from "react";
import { Col, Container, ProgressBar, Row } from "react-bootstrap";
// import Golden_West_Banner from '../../images/Golden-west-banner.png';
// import Profile_img from '../../images/profile.png';
import './Golden_West.css';
import { connect } from 'react-redux';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlayCircle, faHeart, faPlay, faDotCircle } from '@fortawesome/free-solid-svg-icons';
import { getPopProject, beforeProject } from '../ArtistInterface/Create_project_modal/project.action'
import imagePlaceholder from "../../images/image-placeholder.jpg";
import FullPageLoader from "../FullPageLoader/FullPageLoader";
import ParticipateModal from "../participate_modal/participate.modal";
import { useHistory } from "react-router-dom";

function Golden_West(props) {
    const [popProject, setPopProject] = useState();
    const [loader, setLoader] = useState(true)
    const history = useHistory()
    const [smShows, setsmShows] = useState(false);
    const hideModel1 = () => { setsmShows(false) };

    useEffect(() => {
        props.getPopProject();
    }, [])

    useEffect(() => {
        if (props.projects.getProjectAuth) {
            setLoader(false)
            setPopProject(props.projects.project);
            props.beforeProject();
        }
    }, [props.projects.getProjectAuth])

    useEffect(() => {
        if (popProject) {
            setLoader(false)
        }
    }, [popProject])

    useEffect(() => {
        if (props.error)
            setLoader(false)
    }, [props.error])

    return (
        <>
            {loader ? <FullPageLoader /> :
                <div className="golden-bg">
                    <Container>
                        <Row className="golden-head-margin">
                            <Col md={6} xs={8}>
                                <h2 className="sub-heading">Popular Project</h2>
                            </Col>
                            {popProject && <Col md={6} xs={4}>
                                <div className="float-end">
                                    <button type="button" className="btn btn-theme bg-gradient-theme" onClick={() => { history.push("/songs-and-projects") }}>Show all</button>
                                </div>
                            </Col>}
                        </Row>
                        <Row>
                            {!popProject && <Col xs={12}>
                                <div className="home-slider-container">
                                    <span className="text-danger">No data available</span>
                                </div>
                            </Col>}
                            {popProject && <>
                                <Col md={7} xs={12} className="mobile-hide">
                                    <div className="image">
                                        <img src={popProject?.image ? popProject.image : imagePlaceholder} alt="" className="img-fluid" />
                                    </div>
                                </Col>
                                <Col md={5} xs={12}>
                                    <div className="detail mobile-bg-set">
                                        <div className="profile-image">
                                            <img
                                                onClick={() => history.push(`project-detail/${popProject?._id}`)}
                                                src={popProject?.user?.profileImage ? popProject.user.profileImage : imagePlaceholder}
                                                alt=""
                                                className="img-fluid"
                                            />
                                        </div>
                                        <h2 onClick={() => history.push(`project-detail/${popProject?._id}`)} className="sub-heading">{popProject?.name}</h2>
                                        <h5
                                            onClick={() => history.push(`singer-details/${popProject?.user?._id}`)}>{popProject?.user?.username}</h5>
                                        <div className="price-section">
                                            <div className="d-flex flex-column">
                                                <div className="progress-label">{popProject?.currentBudget} ETH <span>/ {popProject?.budgetGoal} ETH</span></div>
                                                {/* <div className="progress">
                                                    <div className="progress-bar" role="progressbar" style={{ width: '25%' }} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100"></div>
                                                </div> */}
                                                <ProgressBar animated now={(popProject?.currentBudget / popProject?.budgetGoal) * 100} />

                                            </div>
                                            {popProject && <button className="btn btn-theme bg-gradient-theme" onClick={() => {
                                                setsmShows(true)
                                            }}>Participate</button>}
                                        </div>
                                    </div>
                                </Col>
                            </>}
                        </Row>
                    </Container>
                    <div className="modal-wrapper">
                        <ParticipateModal hideModel={hideModel1} smShows={smShows} projectId={popProject?._id} updateDashboard={true} />
                    </div>
                </div>}


        </>

    )
}
const mapStateToProps = (state) => ({
    projects: state.projects,
    error: state.error
})

export default connect(mapStateToProps, { getPopProject, beforeProject })(Golden_West);