import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { setWalletAddress } from './Wallet.action';
import { connectToWallet, signRequest } from '../../utils/web3';
import { login, userSignUp } from '../user/user.action';
import { ENV } from '../../config/config';
import { Redirect } from 'react-router-dom';
import { useHistory } from 'react-router';
import * as Icon from "react-icons/fa";
import Checkbox from "react-custom-checkbox";
import { toast } from 'react-toastify';

const Wallet = (props) => {

    const [address, setAddress] = useState()
    const [userData, setUserData] = useState({ address: '', password: '', email: '', username: '' })
    const [error, setError] = useState()
    const [showSignUp, setShowSignUp] = useState(false)
    const [signUpErrors, setSignUpErrors] = useState({
        email: null,
        username: null,
        agree: null
    })
    const history = useHistory()
    const [agree, setAgree] = useState(false);

    useEffect(() => {
        let address = localStorage.getItem("wallet_address")
        if (address) {
            setAddress(address)
        }
    }, [])

    useEffect(() => {
        setAddress(props.wallet)
    }, [props.wallet])

    // useEffect(() => {
    //     // setAddress(props.wallet)
    //     console.log("redirect to login")
    // }, [props.wallet.redirectW])

    useEffect(() => {
        setError(props.error)
    }, [props.error])

    useEffect(() => {
        if (props.user.userData) {
            ENV.encryptUserData(props.user.userData)
        }
    }, [props.user.userData])

    useEffect(() => {
        if (props.user.userAuth) {
            history.push('/profile')
        }
    }, [props.user.userAuth])

    // connect to the metamask 
    const walletConnection = async () => {
        const address = await connectToWallet()
        props.setWalletAddress(address)
        localStorage.setItem("wallet_address", address)
    }

    const onSignIn = async (e) => {
        e.preventDefault()
        const addressSign = await signRequest(address)
        let data = { address, password: addressSign }
        props.login(data)

    }

    const onChange = (e) => {
        const { name, value } = e.target
        setSignUpErrors({ ...signUpErrors, [name]: null });
        let data = userData
        data[name] = value
        setUserData(data)
    }

    const userSignUp = async (e) => {
        e.preventDefault();
        let data = userData;
        // const validation = validateSignUp();
        if (validateSignUp()) {
            const addressSign = await signRequest(address);
            data["password"] = addressSign;
            data["address"] = address;
            setUserData(data);
            props.userSignUp(userData);
        }
        else
            toast.error("SignUp Unsuccessful");
    }

    const validateSignUp = () => {
        let usernameError = false;
        let emailError = false;
        let agreementError = false;
        const obj = { ...signUpErrors };
        if (!ENV.validateEmail(userData.email)) {
            emailError = true;
            obj.email = "Email is not valid";
        }
        if (!userData.username) {
            usernameError = true;
            obj.username = "Username is required";
        }
        if (!agree) {
            agreementError = true;
            obj.agree = "Please agree to privacy policy"
        }

        setSignUpErrors(obj);
        if (usernameError || emailError || agreementError)
            return false
        else return true;
    }

    const handleAgreement = (e) => {
        setSignUpErrors({ ...signUpErrors, agree: null });
        if (e.target.checked)
            setAgree(true)
        else setAgree(false);
    }

    return (
        <section className="wallet-connect-area">
            <div className="container">
                {
                    !address &&
                    <>
                        <div className="row justify-content-center">
                            <div className="col-12">
                                {/* Intro */}
                                <div className="intro text-center">
                                    <span>Connect your Wallet</span>
                                    {/* <h3 className="my-3">Connect your Wallet</h3> */}
                                    <p className='mt-3'>Please select the wallet you want to connect to.</p>
                                </div>
                            </div>
                        </div>
                        <div className="row justify-content-center items ">
                            <div key={`wd_${1}`} className="col-12 col-md-8 item">
                                <div className=" single-wallet">
                                    <a className="d-block text-center text-decoration-none" onClick={() => walletConnection()}>
                                        <img className="avatar-lg" src="/images/metamask.png" alt="" />
                                        <h4 className="my-2 text-white meta-head">Meta Mask</h4>
                                        <p className='mt-3'>A browser extension with great flexibility. The web's most popular wallet</p>
                                    </a>
                                </div>
                            </div>
                        </div>
                    </>
                }
                {
                    !showSignUp && address ?
                        <div className="row justify-content-center">
                            <div className="col-lg-6 col-12">
                                {/* Intro */}
                                <div className="intro text-center">
                                    <span>LOGIN</span>
                                    {/* <h3 className="my-4">Login to your Account</h3> */}
                                    <p className='mt-3'>Login to your Account</p>
                                </div>
                                {/* Item Form */}
                                <div className=" single-wallet-login mt-5">
                                    <form className="item-form no-hover form-margin text-center">
                                        <div className="row model-gap">
                                            <div className="col-12">
                                                <div className="form-group ">
                                                    <span className='address-head'>Address</span>
                                                </div>
                                            </div>
                                            <div className="col-12">
                                                <div className="form-group">
                                                    <p className='wallet-login-address'>{address}</p>
                                                </div>
                                            </div>
                                            {
                                                error && <div className="col-12">
                                                    <p>{error.message}</p>
                                                </div>
                                            }
                                            <div className="col-12 text-center">
                                                <button className=" btn-white right-arrow" onClick={(e) => { onSignIn(e) }}>Sign In</button>
                                            </div>
                                            {
                                                error && <div className="col-12 text-center">
                                                    <button className="btn-white right-arrow" onClick={() => {
                                                        setError(null)
                                                        setShowSignUp(true)
                                                    }}>Would you like to Sign Up?</button>
                                                </div>
                                            }
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div> : ''
                }
                {
                    showSignUp && address ?
                        <div className="row">
                            <div className="col-12">
                                {/* Intro */}
                                <div className="intro text-center">
                                    {/* <span>Signup</span> */}
                                    <h3 className="my-4 pt-5">Create an Account</h3>
                                    {/* <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Laborum obcaecati dignissimos quae quo ad iste ipsum officiis deleniti asperiores sit.</p> */}
                                </div>
                                {/* Item Form */}
                                <div className='signup-form'>
                                    <form className="item-form no-hover" onSubmit={(e) => userSignUp(e)}>
                                        <div className="row model-gap">
                                            <div className="col-12">
                                                <div className="form-group text-center">
                                                    <p className='yellow'>{address}</p>
                                                </div>
                                            </div>
                                            <div className="col-12">
                                                <div className="form-group mt-3">
                                                    <input type="text" className="form-control" name="email" placeholder="Enter your Email" onChange={(e) => { onChange(e) }} />
                                                    {signUpErrors.email && <div style={{ color: 'red' }}>{signUpErrors.email}</div>}
                                                </div>
                                            </div>
                                            <div className="col-12">
                                                <div className="form-group">
                                                    <input type="text" className="form-control" name="username" placeholder="Enter your Username" onChange={(e) => { onChange(e) }} />
                                                    {signUpErrors.username && <div style={{ color: 'red' }}>{signUpErrors.username}</div>}
                                                </div>
                                            </div>
                                            <div className="col-12">
                                                <div className="form-group">
                                                    <div className="form-check form-check-inline text-white">
                                                        {/* <label className="check-container">I agree to <a href="/privacy-policy" className="ms-2 privacy-policy-tag">Privacy Policy</a>
                                                            <input type="checkbox" checked="checked" />
                                                            <span className="checkmark"></span>
                                                        </label> */}
                                                        {/* <Checkbox
                                                            checked={true}
                                                            icon={<img src={require("./check.png")} style={{ width: 24 }} alt="" />}
                                                            borderColor="#D7C629"
                                                            borderRadius={10}
                                                            size={18}
                                                            label="Get em!"
                                                        /> */}
                                                        {/* <Checkbox
                                                            icon={<Icon.FaSquare color="#DE05BA" size={14} />}
                                                            name="my-input"
                                                            onChange={handleAgreement}
                                                            className="check-signin"
                                                            borderColor="#D7C629"
                                                            style={{ cursor: "pointer" }}
                                                            labelStyle={{ marginLeft: 5, userSelect: "none" }}
                                                        />
                                                        <label className="form-check-label ms-2" htmlFor="inlineRadio1">I agree to <a href="/privacy-policy" className="ms-2 privacy-policy-tag">Privacy Policy</a></label> */}
                                                        <input className="form-check-input" type="checkbox" name="inlineCheck" id="inlineCheck1" defaultValue="option1" onChange={handleAgreement} />
                                                        <label className="form-check-label ms-2" htmlFor="inlineCheck1">I agree to <a href="/privacy-policy" className="ms-2 privacy-policy-tag">Privacy Policy</a></label>
                                                    </div>
                                                    {signUpErrors.agree && <div style={{ color: 'red' }}>{signUpErrors.agree}</div>}
                                                </div>
                                            </div>
                                            {
                                                error && <p className="text-danger text-center">{error.message}</p>
                                            }
                                            <div className="col-12 text-center">
                                                <button className="btn-white" type="submit">Sign Up</button>
                                            </div>
                                            {/* <div className="col-12">
                                        <span className="d-block text-center mt-4"  onClick={()=> setShowSignUp(true)} >Already have an account? <p>Login</p></span>
                                    </div> */}
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div> : ""
                }

            </div>
        </section>
    );
}

const mapStateToProps = state => ({
    wallet: state.wallet.accountAddress,
    error: state.error,
    user: state.user
})

export default connect(mapStateToProps, { setWalletAddress, login, userSignUp, })(Wallet);