import React, { useState, useEffect } from "react";
// import './Trending_slide.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlayCircle, faHeart, faPlay, faDotCircle } from '@fortawesome/free-solid-svg-icons';
import { Link, useHistory } from "react-router-dom";
import Nft_slide_modal from '../Nft_slide_modal/Nft_slide_modal.js';
import CartPopup from "../Cart/CartPopup";
import { connect } from "react-redux";
import { beforeHistory, getHistory } from "./Trending_slide.action";
import imagePlaceholder from "../../../images/default-avatar.png";
import music_img from '../../../images/music_item.png';
import moment from 'moment';
import { Button } from "react-bootstrap"
import { ENV } from "../../../config/config";

function Trending_slide(props) {
    const [lgShow, setLgShow] = useState(false);
    const [smShows, setsmShows] = useState(false);
    const [oldActivities, setActivities] = useState([]);
    const [page, setPage] = useState(2);
    const [btnCheck, setBtnCheck] = useState(true);
    const hideModel = () => { setLgShow(false) };
    const hideModel1 = () => { setsmShows(false) };
    const history = useHistory()

    useEffect(() => {
        props.getHistory()
    }, [])

    useEffect(() => {
        if (props.history.historyAuth) {
            const { activities } = props.history.history
            if (activities.length)
                setActivities([...oldActivities, ...activities])
            else
                setBtnCheck(false)
            props.beforeHistory()
        }
    }, [props.history.historyAuth])

    const loadMore = () => {
        const qs = ENV.objectToQueryString({ page });
        setPage(page + 1)
        props.getHistory(null, qs)
    }

    return (
        <>
            <div>
                {
                    oldActivities && oldActivities.length ?
                        oldActivities.map((item, index) => {
                            return (
                                <div className="music-content-history d-flex" key={index}>
                                    <div className="music-history">
                                        {
                                            item.type === 5 ?
                                                <div className="history-main-img">
                                                    <img onClick={
                                                        () => history.push(`/project-detail/${item.projectData[0]._id}`)
                                                    } className="img-fluid rounded main-img" src={item.projectData[0].image ? item.projectData[0].image : music_img} />
                                                </div>
                                                :
                                                <div className="history-main-img">
                                                    <img onClick={
                                                        () => history.push(`/song-detail/${item.nft._id}`)
                                                    } className="img-fluid rounded main-img" src={item.nft.image ? item.nft.image : music_img} />
                                                </div>
                                        }
                                        <div className="history-person-img">
                                            <img onClick={
                                                () => history.push(`/singer-details/${item.user._id}`)
                                            } className="img-fluid rounded" src={item.user.profileImage ? item.user.profileImage : imagePlaceholder} />
                                        </div>
                                    </div>
                                    <div className="history-music-details">
                                        <div className="d-flex">
                                            {
                                                item.type === 1 ? <p className="m-0 music-p-tag">Item created {moment(item.createdAt).fromNow()} by <Link to={`/singer-details/${item.user._id}`} className="d-inline">{item.user.username}</Link>{" "}{item.price ? ", " : ''}{item.price ? "having price: " : ''}<strong>{item.price}{item.price ? ' ' : ''}{item.currency ? item.currency : ' Eth'}</strong></p> :
                                                    item.type === 5 ? <p className="m-0 music-p-tag"><Link to={`/singer-details/${item.user._id}`} className="d-inline">{item.user.username}</Link> invested/participated in <Link to={`/project-detail/${item.projectData[0]._id}`}>{item.projectData[0].name}</Link>{" "}{moment(item.createdAt).fromNow()}</p> :
                                                        ''
                                            }
                                        </div>
                                        <div className="d-flex icons-row">
                                            <button type="button" className="btn btn-theme d-flex bg-gradient-green" onClick={() => setsmShows(true)}>
                                                Transation detail
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            )
                        })
                        :
                        <div className="home-slider-container">
                            <span className="text-danger text-center">No History Found.</span>
                        </div>
                }
            </div>
            <div className="modal-wrapper">
                <Nft_slide_modal hideModel={hideModel} lgShow={lgShow} />
                {/* <CartPopup hideModel={hideModel1} smShows={smShows} /> */}
            </div>
            <hr />
            {
                btnCheck ?
                    <div className="load-more">
                        <Button className="btn-dark bg-gradient-green" onClick={loadMore}>Load more</Button>
                    </div>
                    : ''
            }
        </>
    )
}

const mapStateToProps = (state) => ({
    history: state.history
})
export default connect(mapStateToProps, { beforeHistory, getHistory })(Trending_slide);