import { BEFORE_HISTORY, GET_HISTORY } from "../../../redux/types";

const initialState = {
    history: null,
    historyAuth: false
}

export default function (state = initialState, action) {
    switch (action.type) {
        case GET_HISTORY:
            return {
                ...state,
                history: action.payload,
                historyAuth: true
            }
        case BEFORE_HISTORY:
            return {
                ...state,
                history: null,
                historyAuth: false
            }
        default:
            return {
                ...state
            }
    }
}