import { BEFORE_SINGER, GET_SINGERS, GET_ERRORS, GET_SINGER_DETAILS, GET_SINGER_DASH_INFO } from "../../redux/types";

const initialState = {
    singer: null,
    singers: [],
    pagination: null,
    deleteSingerDetailAuth: false,
    upsertSingerDetailAuth: false,
    getSingerDetailAuth: false,
    getSingersAuth: false,
    totalFollowers: null,
    weeklyFollowers: null,
    totalTracksSold: null,
    weeklyTracksSold: null,
    uniqueVisitors: null,
    returningVisitors: null
}

export default function (state = initialState, action) {
    switch (action.type) {
        case GET_SINGER_DETAILS:
            return {
                ...state,
                singer: action.payload,
                getSingerDetailAuth: true
            }

        case GET_SINGER_DASH_INFO:
            return {
                ...state,
                totalFollowers: action.payload.totalFollowers,
                weeklyFollowers: action.payload.weeklyFollowers,
                totalTracksSold: action.payload.totalTracksSold,
                weeklyTracksSold: action.payload.weeklyTracksSold,
                uniqueVisitors: action.payload.uniqueVisitors,
                returningVisitors: action.payload.returningVisitors,
                getSingerDetailAuth: true
            }
        case GET_SINGERS:
            return {
                ...state,
                singers: action.payload.users,
                pagination: action.payload.pagination,
                getSingersAuth: true,
            }
        case BEFORE_SINGER:
            return {
                ...state,
                singer: null,
                singers: [],
                pagination: null,
                deleteSingerDetailAuth: false,
                upsertSingerDetailAuth: false,
                getSingerDetailAuth: false,
                totalFollowers: null,
                weeklyFollowers: null,
                getSingersAuth: false,
                totalTracksSold: null,
                weeklyTracksSold: null,
                uniqueVisitors: null,
                returningVisitors: null
            }
        default:
            return {
                ...state
            }
    }
}