import React, { useState, useEffect } from "react";
import { Link, useHistory } from "react-router-dom";
import { Col, Container, Row, Navbar, Dropdown } from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCompass, faExchangeAlt, faPlayCircle, faSearch, faUserPlus, faUserCircle } from '@fortawesome/free-solid-svg-icons';
import './Header.css';
import { setLoggedInUser } from '../../components/user/user.action';
import { setWalletAddress, setBeforeWallet } from '../../components/Wallet/Wallet.action';
import { beforeUser } from '../../components/user/user.action';
import imagePlaceholder from "../../images/default-avatar.png";
import Wallet from '../../components/Wallet/Wallet';
import DroppdownPopup from '../DropdownPopup/DropdownPopup.js'
import { ENV } from '../../config/config';
import { connect } from 'react-redux';

function Header(props) {
    const [isopen, setisopen] = useState(false);
    const isopenhide = () => { setisopen(false) }
    // const [userAuth, setUserAuth] = useState()
    const [wallet, setWallet] = useState()

    const history = useHistory();
    const pathname = history.location.pathname;

    const formattedAddress = (address) => {
        return `${address.slice(0, 5)}...${address.slice(-5)}`
    }


    useEffect(() => {
        const address = localStorage.getItem("wallet_address")
        const user = localStorage.getItem("encuse");
        if (address)
            props.setWalletAddress(address)
        if (user)
            props.setLoggedInUser();
    }, [])

    // useEffect(() => {

    //     if (props?.user?.userData?.userAuth) {
    //         // store the user in localstorage
    //         let obj = props.user
    //         obj["userAuth"] = props?.user?.userData?.userAuth
    //         ENV.encryptUserData(obj)

    //     }
    // }, [props?.user?.userData?.userAuth])


    useEffect(() => {
        if (props.user.userAuth) {
            // store the user in localstorage
            let { userData } = props.user
            ENV.encryptUserData(userData);
        }
    }, [props.user.userAuth])

    useEffect(() => {
        if (props.wallet) {
            setWallet(props.wallet)
        }
    }, [props.wallet])


    useEffect(() => {
        // setAddress(props.wallet)
        if (props.walletObj.redirectW) {
            props.beforeUser()
            props.setBeforeWallet()
            history.push('/login')
        }
    }, [props.walletObj.redirectW])

    return (
        <>
            <header id="header">
                <Navbar collapseOnSelect expand="lg" className="pos-stat navbar-dark">
                    <Container>
                        <Navbar.Brand className="logo-img" href="/"><img className="img-fluid" src="/images/logo.svg" alt="missing" /></Navbar.Brand>
                        <Navbar.Toggle aria-controls="basic-navbar-nav" />
                        <Navbar.Collapse id="basic-navbar-nav">
                            <ul className="navbar-nav m-auto">
                                <span className="material-icons-outlined"></span>
                                <li className={`nav-item ${pathname === '/' ? 'active' : ''}`}><Link className="nav-link" to="/"><i className="nav-icon"><FontAwesomeIcon icon={faCompass} /></i>Discover </Link></li>
                                <li className={`nav-item ${pathname === '/trade' ? 'active' : ''}`}><Link className="nav-link" to="/trade"><i className="nav-icon"><FontAwesomeIcon icon={faExchangeAlt} /></i>Trade </Link></li>
                                <li className={`nav-item ${pathname === '/artists' ? 'active' : ''}`}><Link className="nav-link" to="/authors-list"><i className="nav-icon"><FontAwesomeIcon icon={faUserCircle} /></i>Artists</Link></li>
                            </ul>
                        </Navbar.Collapse>
                        {wallet && props.user.userAuth && <div className="user-icon float-end">
                            <img className="img-fluid" onClick={() => setisopen(true)} src={props?.user?.userData?.profileImage ? props.user.userData.profileImage : imagePlaceholder} alt="missing Profile Pic" />
                        </div>}
                        <div className="head-button">
                            {
                                wallet && props.user.userAuth ?
                                    (<Dropdown className="ms-4">
                                        <Dropdown.Toggle id="dropdown-basic" className="btn-white-1">
                                            <span className="header-wallet-address text-black">{formattedAddress(wallet)}</span>

                                        </Dropdown.Toggle>

                                        <Dropdown.Menu>
                                            <Dropdown.Item as={Link} to="/profile">Profile</Dropdown.Item>
                                            {/* <Dropdown.Item as={Link} to="/authors-list">Artists</Dropdown.Item> */}
                                            <Dropdown.Item as={Link} to="/my-albums">My Albums</Dropdown.Item>
                                            <Dropdown.Item as={Link} to="/library">Library</Dropdown.Item>
                                            <Dropdown.Item as={Link} to="/logout">Logout</Dropdown.Item>
                                        </Dropdown.Menu>
                                    </Dropdown>)
                                    :
                                    <button type="button" className="btn-white right-arrow" data-toggle="modal" data-target="#connectWallet1" onClick={() => {
                                        history.push("/login")
                                    }
                                    }>
                                        <span className="connect-icon text-black"><FontAwesomeIcon icon={faUserPlus} className="connect-in-icon"></FontAwesomeIcon></span>
                                        <span className="connect-span text-black">CONNECT</span>
                                    </button>
                            }
                        </div>
                    </Container>
                </Navbar>
                <DroppdownPopup isopenhide={isopenhide} isopen={isopen} formattedAddress={wallet} />
            </header>

        </>
    )
}
const mapStateToProps = state => ({
    user: state.user,
    wallet: state.wallet.accountAddress,
    walletObj: state.wallet
})
// export default Header;
export default connect(mapStateToProps, { setLoggedInUser, setWalletAddress, setBeforeWallet, beforeUser })(Header);