import React, { useEffect, useState } from "react";
import { Col, Container, Row, Tab, Tabs, Button } from "react-bootstrap";
import Header from "../Header/Header";
import SongImg2 from '../../images/Justin-Bieber.jpg';
import { connect } from "react-redux";
import { getSingers, beforeSinger } from '../../components/SingerDetails/singer.action'
import { faArrowLeft, faUsers, faCheckCircle } from '@fortawesome/free-solid-svg-icons';
import FullPageLoader from '../FullPageLoader/FullPageLoader'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import LibraryTabs from "../LibraryTabs/LibraryTabs";


function AuthorsList(props) {
    const [fullLoader, setFullLoader] = useState(true);
    const [artists, setArtists] = useState([]);

    useEffect(() => {
        props.getSingers('get-artists');
    }, [])

    useEffect(() => {
        if (props.singers.getSingersAuth) {
            setArtists(props.singers.singers)
            props.beforeSinger()
            setFullLoader(false);
        }
    }, [props.singers.getSingersAuth])

    useEffect(() => {
        if (props.error)
            setFullLoader(false)
    }, [props.error])

    return (
        <>
            {fullLoader && < FullPageLoader />
            }
            <div className="authors-list">
                <Container>
                    <Row className="justify-content-center margin-bottom">
                        <Col lg={7} md={8}>
                            <div className="intro text-center">
                                <h1 className="title">Our Artists</h1>
                            </div>
                        </Col>
                    </Row>
                    {(artists && artists?.length > 0) ?
                        <Row className="mt-5">
                            {artists?.map(artist => {
                                return (<Col lg={3} md={4} sm={6} key={artist._id}>
                                    <div className="card no-hover text-center mt-3">
                                        <div className="image-over">
                                            <a href={`singer-details/${artist._id}`}>
                                                <img className="card-img-top" src={artist.profileImage ? artist.profileImage : SongImg2} alt="Author profile" />
                                            </a>
                                        </div>
                                        <div className="card-caption col-12 p-0">
                                            {artist?.identityVerification == 0 && <div className="singer-w-address mt-3">
                                                {/* <span className="email-label">Wallet Address:</span> */}
                                                <span className="ans-label"><FontAwesomeIcon icon={faCheckCircle} className="tick-icon text-success"></FontAwesomeIcon></span>
                                            </div>}
                                            <div className="card-body mt-2 pb-0">
                                                <a href={`singer-details/${artist._id}`}>
                                                    <h5>{artist?.username}</h5>
                                                    {/* <img className="card-img-top" src={artist.profileImage} alt="Author profile" /> */}
                                                    {/* <img className="card-img-top" src={SongImg2} alt="Author profile" /> */}
                                                </a>
                                                <p className="d-flex align-items-center justify-content-center gap-2 m-0"><span><FontAwesomeIcon icon={faUsers}></FontAwesomeIcon></span> {artist?.totalNumberOfFollowers}</p>
                                                {/* <div className="social-icons d-flex justify-content-center my-3">
                                            </div> */}
                                            </div>
                                        </div>
                                    </div>
                                </Col>)
                            })
                            }
                        </Row>
                        :

                        <Row className="mt-4">
                            <Col>
                                <div className="home-slider-container"><span className="text-danger">No Author Found</span></div>
                            </Col>
                        </Row>
                    }
                </Container>
            </div>

        </>
    )
}

const mapStateToProps = (state) => {
    return {
        singers: state.singers,
        error: state.error

    }
}

export default connect(mapStateToProps, { getSingers, beforeSinger })(AuthorsList);