import { BEFORE_FOLLOW, DO_SINGER_FOLLOW, CHECK_FOLLOWERSHIP,DO_SINGER_UNFOLLOW } from "../../redux/types";

const initialState = {
    follow: null,
    pagination: null,
    doSignerFollowAuth: false,
    checkFollowerShipAuth: false,
    doSignerUnFollowAuth:false
}

export default function (state = initialState, action) {
    switch (action.type) {
        case DO_SINGER_FOLLOW:
            return {
                ...state,
                follow: action.payload,
                doSignerFollowAuth: true
            }
        case DO_SINGER_UNFOLLOW:
            return {
                ...state,
                follow: action.payload,
                doSignerUnFollowAuth: true
            }
        case CHECK_FOLLOWERSHIP:
            return {
                ...state,
                follow: action.payload,
                checkFollowerShipAuth: true
            }
        case BEFORE_FOLLOW:
            return {
                ...state,
                follow: null,
                pagination: null,
                doSignerFollowAuth: false,
                checkFollowerShipAuth: false,
                doSignerUnFollowAuth:false
            }
        default:
            return {
                ...state
            }
    }
}