import { emptyError } from '../../redux/shared/error/error.action';
import { ENV } from '../../config/config';
import { GET_RECENT_NFTS, BEFORE_RECENT_NFTS, GET_ERRORS } from "../../redux/types";

export const beforeRecentNFT = () => {
    return {
        type: BEFORE_RECENT_NFTS
    }
}

export const getRecentNfts = (qs = '') => dispatch => {
    dispatch(emptyError());
    let url = `${ENV.url}nfts/getRecent`;
    if (qs)
        url += `?${qs}`

    fetch(url, {
        method: 'GET',
        headers: {
            'content-type': 'application/json',
            'Authorization': ENV.Authorization,
            'x-auth-token': ENV.x_auth_token
        }
    }).then(res => res.json()).then(data => {
        if (data.success) {
            // if (qs === 0) 
            // toast.success(data.message)
            dispatch({
                type: GET_RECENT_NFTS,
                payload: data.data
            })
        } else {
            // if (qs !== 0 )
            // toast.error(data.message)
            dispatch({
                type: GET_ERRORS,
                payload: data
            })
        }
    }).catch(error => {
        if (error.response && error.response.data) {
            const { data } = error.response
            // if (data.message)
            //     toast.error(data.message)
        }
        dispatch({
            type: GET_ERRORS,
            payload: error
        })
    })
};

