import React, { useState, useEffect } from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import Header from '../Header/Header';
// import './Home.css';
import Banner from '../Banner/Banner';
import Nft from '../NFT_Slider/Nft.js';
import Trending_slider from '../Trending/Trending_slider';
import ArtistOverview from "../ArtistOverview/ArtistOverview";
import { connect } from "react-redux";
import { beforeSinger, getArtistDashInfo } from "../../SingerDetails/singer.action";
import { beforeNFT, getPopNFTs } from "../Upload_create_modal/nft.action";
import FullPageLoader from "../../FullPageLoader/FullPageLoader";
import { ENV } from "../../../config/config";

function Home(props) {
   const [singerDetails, setSingerDetails] = useState();
   const [popNfts, setPopNfts] = useState();
   const [loader, setLoader] = useState(true);

   useEffect(() => {
      props.getArtistDashInfo();
      const user = ENV.getUserKeys('_id');
      const qs = ENV.objectToQueryString({ userId: user._id });
      props.getPopNFTs(qs);
   }, [])

   useEffect(() => {
      if (props.singers.getSingerDetailAuth) {
         setSingerDetails({
            totalFollowers: props.singers.totalFollowers,
            weeklyFollowers: props.singers.weeklyFollowers,
            totalTracksSold: props.singers.totalTracksSold,
            weeklyTracksSold: props.singers.weeklyTracksSold,
            uniqueVisitors: props.singers.uniqueVisitors,
            returningVisitors: props.singers.returningVisitors
         });
         props.beforeSinger();
      }
   }, [props.singers.getSingerDetailAuth])

   useEffect(() => {
      if (props.nfts.getNFTAuth) {
         setPopNfts(props.nfts.nfts);
         props.beforeNFT();
      }
   }, [props.nfts.getNFTAuth])

   useEffect(() => {
      if (singerDetails && popNfts)
         setLoader(false);
   }, [singerDetails, popNfts])

   useEffect(() => {
      if (props.error)
         setLoader(false)
   }, [props.error])

   return (
      <>
         {
            loader ?
               <FullPageLoader /> :
               <div className="artist-dashboard">
                  {/* <Header /> */}
                  <div className="banner-section main-padding-top">

                     {/* <Banner /> */}
                     <ArtistOverview singerDetails={singerDetails} />
                  </div>
                  <div className="top-section">

                     < Nft nfts={popNfts} />
                  </div>

                  <div className="sec-bg-gradient">
                     <Trending_slider />
                  </div>

               </div>
         }
      </>
   )
}

const mapStateToProps = (state) => ({
   singers: state.singers,
   nfts: state.nfts,
   error: state.error
})

export default connect(mapStateToProps, { beforeSinger, getArtistDashInfo, beforeNFT, getPopNFTs })(Home);