import React, { useState, useEffect } from "react";
import { Col, Container, Row, Tab, Tabs } from "react-bootstrap";
import Header from "../Header/Header";
import { getNFTs } from '../ArtistInterface/Upload_create_modal/nft.action';
import { upsertAlbum, beforeAlbum } from "./album.action";
import { connect } from 'react-redux';
import Form from 'react-bootstrap/Form';
import FullPageLoader from "../FullPageLoader/FullPageLoader";
import { Link } from 'react-router-dom'
import Img250 from '../../images/dummy-placeholder-image-400x400.jpg';
import Img670 from '../../images/img-placeholder-large.jpg';
import Img955 from '../../images/img-placeholder-large.jpg';

function CreateAlbum(props) {


    const initialAlbumObject = {
        name: '',
        description: '',
        image: null,
        banner: null,
        logo: null
    }
    const [album, setAlbum] = useState(initialAlbumObject)
    const initialImgNameObj = {
        image: 'Choose Featured Image',
        logo: 'Choose Logo *',
        banner: 'Select Banner Image'
    }
    const [selectedImgName, setSelectedImgName] = useState(initialImgNameObj);
    const [fullLoader, setFullLoader] = useState(false)
    const [error, setError] = useState({})



    const handleChange = (e) => {
        const { name, value } = e.target;
        const data = album;
        data[name] = value;

        setAlbum({ ...data })

        let errorObj = error;
        delete errorObj[e.target.name]
        setError({ ...errorObj })
    }
    const handleImageChange = (e) => {
        let file = e.target.files[0]
        let fileName = e.target.files.item(0).name;
        let fileId = e.target.id
        let data = album
        data = { ...data, [e.target.name]: file }
        // setSelectedImgName({ ...selectedImgName, [e.target.name]: fileName })
        setAlbum(data)

        let errorObj = error;
        delete errorObj[e.target.name]
        setError({ ...errorObj })
    }
    const handleSubmit = async () => {
        const isValidate = validate()
        if (!isValidate) {
            window.scrollTo(0, 0)
            return
        };
        //Append intoform Data 
        var formData = new FormData()
        for (const key in album)
            formData.append(key, album[key])

        await props.upsertAlbum('/album/create', formData);
        setFullLoader(true);
    }

    const validate = () => {
        if (album.name == '') {
            error.name = "Name is Required"
        }
        if (album.description == '') {
            error.description = "Description is Required"
        }
        if (album.logo == null) {
            error.logo = "Logo is Required"
        }
        // if (album.image == null) {
        //     error.image = "Featured Image is Required"
        // }
        if (Object.keys(error).length === 0) {
            return true;
        } else {
            setError({ ...error })
            return false;
        }
    }
    useEffect(() => {
        if (props.albums.upsertAlbumAuth) {
            props.beforeAlbum()
            setAlbum(initialAlbumObject)
            setSelectedImgName(selectedImgName)
            setFullLoader(false)
            props.history.push("/my-albums")
        }

    }, [props.albums.upsertAlbumAuth])
    useEffect(() => {
        if (props?.error)
            setFullLoader(false)
    }, [props?.error])

    return (
        <>
            <div className="create-album">
                {fullLoader ? <FullPageLoader /> : ""}

                <Container>
                    <div className="create-album-section">
                        <div className="row justify-content-between">
                            <div className="col-12 col-md-12 mt-5">
                                <div className="mt-5 mt-lg-0 mb-4 mb-lg-5">
                                    <div className="intro">
                                        <div className="intro-content">
                                            <h3 className="mt-3 mb-0 text-white">Create Album</h3>
                                        </div>
                                    </div>
                                </div>
                                <form id="create-collection" className="item-form card no-hover">
                                    {Object.keys(error).length !== 0 && <span className="text-danger">Please Provide All Required Fields</span>}
                                    <div className="row">
                                        <div className="col-12 mt-4">
                                            <div className="input-group form-group">
                                                <Form.Group controlId="formFileLg" className="mb-3">
                                                    <Form.Label>{selectedImgName.logo}</Form.Label>
                                                    <Form.Control type="file" size="lg" className="mt-2 input-file" name="logo" onChange={handleImageChange} />
                                                </Form.Group>
                                                {error?.logo && <span className="text-danger">{error.logo}</span>}
                                            </div>
                                        </div>
                                        <div className="col-12 mt-2">
                                            <div className="image-holder-1 no-hover text-center album">
                                                <img id="logo-placeholder" className="card-img-top create-collection-placeholder" src={album.logo ? URL.createObjectURL(album.logo) : `${Img250}`} alt="" />
                                            </div>
                                        </div>
                                        <div className="col-12 mt-4">
                                            <div className="input-group form-group">
                                                <Form.Group controlId="formFileLg" className="mb-3">
                                                    <Form.Label>{selectedImgName.image}</Form.Label>
                                                    <Form.Control type="file" size="lg" className="mt-2 input-file" name="image" onChange={handleImageChange} />
                                                </Form.Group>
                                                {error?.image && <span className="text-danger">{error.image}</span>}

                                            </div>
                                        </div>
                                        <div className="col-12 mt-4">
                                            <div className="image-holder-2 no-hover text-center featured">
                                                <img id="featuredImg-placeholder" className="card-img-top create-collection-placeholder" src={album.image ? URL.createObjectURL(album.image) : `${Img670}`} alt="" />
                                            </div>
                                        </div>
                                        <div className="col-12 mt-4">
                                            <Form.Group controlId="formFileLg" className="mb-3">
                                                <Form.Label>{selectedImgName.banner}</Form.Label>
                                                <Form.Control type="file" size="lg" className="mt-2 input-file" name="banner" onChange={handleImageChange} />
                                            </Form.Group>
                                        </div>
                                        <div className="col-12 mt-4">
                                            <div className="image-holder-3 no-hover text-center banner">
                                                <img id="banner-placeholder" className="card-img-top create-collection-placeholder" src={album.banner ? URL.createObjectURL(album.banner) : `${Img955}`} alt="" />
                                            </div>
                                        </div>
                                        <div className="col-12 mt-4">
                                            <div className="form-group mt-3">
                                                <input type="text" className="form-control" name="name" placeholder="Name *" value={album.name} name="name" onChange={handleChange} />
                                                <span className="text-danger"></span>
                                            </div>
                                            {error?.name && <span className="text-danger">{error.name}</span>}

                                        </div>
                                        {/* <div className="col-6 mt-4">
                                            <div className="form-group mt-3">
                                                <input type="text" className="form-control" name="url" placeholder="URL" value="" />
                                            </div>
                                        </div> */}
                                        {/* <div className="col-12 mt-4">
                                            <div className="form-group">
                                                <span className="no-padding">
                                                    <select className="form-control" id="category" name="categoryId">
                                                        <option value="">Select Category</option>
                                                        <option value="61a6128cb565972707bec4ef">Art</option>
                                                        <option value="61a612c4b565972707bec4f1">Collectibles</option>
                                                    </select>
                                                </span>
                                                <span className="text-danger">
                                                </span>
                                            </div>
                                        </div> */}
                                        <div className="col-12 mt-4">
                                            <div className="form-group">
                                                <textarea className="form-control" name="description" placeholder="Description *" cols="30" rows="3" name="description" value={album.description} onChange={handleChange}></textarea>
                                            </div>
                                            {error?.description && <span className="text-danger">{error.description}</span>}

                                        </div>
                                        <div className="col-12 mt-4">
                                            <button className="btn w-100 mt-3 mt-sm-4 album-btn bg-gradient-theme" type="button" onClick={handleSubmit}>Create Album</button>
                                            <Link className="btn btn-grey w-100 mt-3 mt-sm-4 cancel-btn" to="/my-albums">Cancel</Link>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </Container>
            </div>

        </>
    )
}


const mapStateToProps = (state) => ({
    albums: state.albums,
    error: state.error

})

export default connect(mapStateToProps, { upsertAlbum, beforeAlbum })(CreateAlbum);