export const getProject = (id, qs = '') => dispatch => {
    dispatch(emptyError());
    let url = `${ENV.url}project/get/${id}`;
    if (qs)
        url += `?${qs}`

    fetch(url, {
        method: 'GET',
        headers: {
            'content-type': 'application/json',
            'Authorization': ENV.Authorization,
            'x-auth-token': ENV.x_auth_token
        }
    }).then(res => res.json()).then(data => {
        if (data.success) {
            // if (!qs)
            //     toast.success(data.message)
            dispatch({
                type: GET_PROJECT,
                payload: data.data
            })
        } else {
            // if (!qs)
            //     toast.error(data.message)
            dispatch({
                type: GET_ERRORS,
                payload: data
            })
        }
    }).catch(error => {
        if (error.response && error.response.data) {
            const { data } = error.response
            // if (data.message)
            //     toast.error(data.message)
        }
        dispatch({
            type: GET_ERRORS,
            payload: error
        })
    })
};

