import React, { useState, useEffect } from "react";
import { faTimes, faDollarSign, faMusic } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import './DropdownPopup.css';
import { ToggleButtonGroup, ToggleButton } from "react-bootstrap";
import { Link } from "react-router-dom";
import $ from 'jquery';
import { ENV } from '../../config/config';
// import { setUser, getUser } from '../../components/user/user.action';
import { connect } from 'react-redux'
import { getBalance } from '../../utils/web3';
import { setWalletAddress } from "../Wallet/Wallet.action";
import imagePlaceholder from "../../images/default-avatar.png";
import { useHistory } from 'react-router-dom'
const currencies = (ENV.currencies).filter(elem => elem.showInProfileCard)
import Verified from '../ArtistInterface/Get_verified_modal/Get_verified_modal'
import { setLoggedInUser } from '../../components/user/user.action'
import { getIdentity } from '../ArtistInterface/Get_verified_modal/identity.action'



function Dropdown(props) {
    const [WalletBalance, setWalletBalance] = useState(0);
    const [userData, setUserData] = useState({})
    var pathname = window.location.pathname.split('/');

    useEffect(() => {
        let user = ENV.getUserKeys('accessToken _id profileImage username totalAmountUserInvesting nftsCount address')
        // console.log("user", user)
        setUserData({ ...user })
    }, [])
    useEffect(() => {
        props.getIdentity()
    }, [props.identity.upsertIdentityAuth])


    const lightFunction = () => {
        $("body").removeClass("dark-mode");
        $("body").addClass("light-mode");
        // $("#tbg-radio-2").attr("checked", true);
        // $("#tbg-radio-3").attr("checked", false);
    }
    const darkFunction = () => {
        $("body").removeClass("light-mode");
        $("body").addClass("dark-mode");
        // $("#tbg-radio-2").attr("checked", false);
        // $("#tbg-radio-3").attr("checked", true);
    }

    const formattedAddress = (address) => {
        return `${address?.slice(0, 5)}...${address?.slice(-5)}`
    }


    useEffect(() => {
        fetchWalletBalance();
    }, [])

    // useEffect(() => {
    //     if (props.user.userAuth === true)
    //         setUserData(props.user.userData)
    // }, [props.user.userAuth])

    const [lgShows, setLgShows] = useState(false);
    const hideModels = () => { setLgShows(false) }

    const fetchWalletBalance = async () => {
        let balance = await getBalance();
        setWalletBalance(balance);
    }

    return (
        <>
            {props.isopen ?
                <div className="dropdown-popup">
                    <div className="dropdown-content">
                        <div className="p-4 overflow-hidden">
                            <div className="user-icon" >
                                <img className="img-fluid" src={userData?.profileImage ? userData.profileImage : imagePlaceholder} alt="missing Profile Pic" />
                            </div>
                            <div className="cross-btn float-end" onClick={() => props.isopenhide()}>
                                <FontAwesomeIcon icon={faTimes} />
                            </div>

                            <div className="popup-head mt-3">
                                <h2 className="sub-heading mb-3">{userData?.username}</h2>
                            </div>


                            <div className="d-flex align-items-center mt-2">
                                <h3 className="account-name"><span className="purple">Wallet</span> <span className="yellow">Account</span> :</h3> <span><h4 className="amount">{userData?.address ? formattedAddress(userData?.address) : "N/A"}</h4></span>

                            </div>

                            <div className="d-flex align-items-center mt-1">
                                <h3 className="account-name"><span className="purple">Wallet</span> <span className="yellow">Balance</span> : </h3> <span><h4 className="amount">{WalletBalance && userData?.address ? WalletBalance?.slice(0, 10) : 0} {currencies && currencies.length ? currencies[0].value : ''}</h4></span>
                            </div>

                        </div>
                        <div className="p-4 border-top">
                            <div className="d-flex justify-content-between imgs-row">
                                <div className="img-box">
                                    <span><FontAwesomeIcon icon={faDollarSign} className="dollar-icon" /></span>
                                    <span>Total Invest {userData?.totalAmountUserInvesting ? userData.totalAmountUserInvesting : 0}</span>
                                </div>
                                <div className="img-box">
                                    <span><FontAwesomeIcon icon={faMusic} className="nft-icon" /></span>
                                    <span>Total Nfts Own {userData?.nftsCount ? userData.nftsCount : 0}</span>
                                </div>
                            </div>
                            <div className="mt-3 mb-1 payment-history-link">
                                <Link to="/payment-history">History</Link>
                            </div>
                            <div className="mt-3 mb-2 payment-history-link text-center">
                                <span className="purple-dark">Preferences</span>
                            </div>
                        </div>
                        <div className="p-4 border-top">
                            <div className="theme-row d-flex justify-content-between">
                                <h5>Theme</h5>
                                <div className="theme-buttons">
                                    <ToggleButtonGroup type="radio" name="options" defaultValue={3}>
                                        <ToggleButton id="tbg-radio-2" value={2} onClick={() => lightFunction()}>
                                            Light
                                        </ToggleButton>
                                        <ToggleButton id="tbg-radio-3" value={3} onClick={() => darkFunction()} >
                                            Dark
                                        </ToggleButton>
                                    </ToggleButtonGroup>
                                </div>
                            </div>
                        </div>
                        <div className="p-4 border-top text-center">
                            {pathname && pathname[1] == "artist" && <div className="mb-3 text-center upload-btn">
                                {
                                    <button type="button" disabled={
                                        props?.identity?.identity?.status === 0 ? true : props?.identity?.identity?.status === 2 ? true : false
                                    } className="btn btn-theme upload-icon d-flex bg-gradient-green m-auto" onClick={() => setLgShows(true)}>{props?.identity?.identity?.status === 1 ? "Verification Rejected Apply Again" : props?.identity?.identity == null ? "Upload / Create" : props?.identity?.identity?.status === 2 ? "verification Request Received" : props?.identity?.identity?.status === 0 && "Request Approved Successfully"}</button>
                                }
                            </div>}

                            {pathname && pathname[1] == "artist" ? <Link to="/" className="switch-account">
                                Switch to Home
                            </Link> :
                                <Link to="/artist/dashboard" className="switch-account">
                                    Switch to artist account
                                </Link>


                            }
                        </div>
                    </div>
                    <div className="modal-wrapper">
                        <Verified hideModel={hideModels} lgShows={lgShows} />
                    </div>
                </div>
                : ''}
        </>
    )

}
const mapStateToProps = state => ({
    user: state.user,
    identity: state.identity
})
export default connect(mapStateToProps, { setLoggedInUser, getIdentity })(Dropdown);
