export const likeNFT = (id = '', qs = '') => dispatch => {
    dispatch(emptyError());
    const user = ENV.getUserKeys('_id');
    let url = `${ENV.url}nfts/like`;
    if (qs)
        url += `?${qs}`

    fetch(url, {
        method: 'PUT',
        headers: {
            'content-type': 'application/json',
            'Authorization': ENV.Authorization,
            'x-auth-token': ENV.x_auth_token
        },
        body: JSON.stringify({ userId: user._id, nftId: id })
    }).then(res => res.json()).then(data => {
        if (data.success) {
            // if (!qs)
            //     toast.success(data.message)
            toast.success(data.message)
            dispatch({
                type: LIKE_NFT,
                payload: data.data
            })
        } else {
            // if (!qs)
            //     toast.error(data.message)
            dispatch({
                type: GET_ERRORS,
                payload: data
            })
        }
    }).catch(error => {
        if (error.response && error.response.data) {
            const { data } = error.response
            // if (data.message)
            //     toast.error(data.message)
        }
        dispatch({
            type: GET_ERRORS,
            payload: error
        })
    })
};

