import React, { useState, useEffect } from "react";
import { Col, Container, Row, Dropdown } from "react-bootstrap";
import { Modal, Button, } from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlayCircle, faHeart, faPlay, faDotCircle, } from '@fortawesome/free-solid-svg-icons';
import PayOrPlayPopup from "../Pay/PayOrPlayPopup";
import CartPopup from "../Cart/CartPopup";
import { useHistory } from "react-router-dom";
import imagePlaceholder from '../../images/image-placeholder.jpg';
import { Link } from "react-router-dom";
import BulkBuyPopUp from "../bulkBuy/bulkBuy";
import { ENV } from "../../config/config";

function DiscoverMusicNft(props) {
    const [lgShows, setLgShows] = useState(false);
    const hideModels = () => { setLgShows(false) };
    const history = useHistory();
    const [bulkBuyShow, setBulkBuyShow] = useState(false);
    const [smShows, setsmShows] = useState(false);
    const hideModel1 = () => { setsmShows(false) };
    const hideModel2 = () => { setBulkBuyShow(false) };
    const [authUser, setAuthUser] = useState({})
    useEffect(() => {
        let user = ENV.getUserKeys('_id')
        setAuthUser({ ...user })
    }, [])
    // const {name,image,description}=props?.nft;
    return (
        <>
            <div className="slide-content my-3">
                <div className="card-background">
                    <img className="card-image" src={props.nft?.image ? props.nft.image : imagePlaceholder} alt="missing" />
                </div>
                <div className="card-inner">
                    <div className="slide-head text-center">
                        {/* <span className="play-icon"><FontAwesomeIcon icon={faPlayCircle} /></span> */}
                        <button onClick={() => history.push(`/song-detail/${props?.nft?._id}`)} className="nft-detail-button">{props.nft?.name}</button>
                    </div>
                    <div className="slide-head text-center">
                        <Link to={`/singer-details/${props.nft?.creator?._id}`} className="detail-button">{props.nft?.creator?.username}</Link>
                    </div>
                    <div className="d-flex justify-content-between  client-details ">
                        {/* <h5 className="client-name">{props?.nft?.image}</h5> */}
                    </div>
                    <div className="d-flex flex-column justify-content-between slide-bottom gap-4">
                        <div className="d-flex icons-row justify-content-center">
                            <div className="like">
                                <FontAwesomeIcon className="text-white" icon={faHeart} />
                                <span>{props.nft?.likes}</span>
                            </div>
                            <div className="view">
                                <FontAwesomeIcon className="text-white" icon={faPlay} />
                                <span>{props.nft?.timesPlayed}</span>
                            </div>
                            {props.nft?.track && <div className="subtract">
                                <span><FontAwesomeIcon className="text-white" icon={faDotCircle} /></span>
                                <span>{props.nft?.track} / {props.nft?.totalTracks}</span>
                            </div>}
                        </div>
                        <div className="button-aligns d-flex justify-content-center">
                            {/* <button type="button" className="btn btn-theme ethereum-icon d-flex bg-gradient-theme" onClick={() => setLgShows(true)}> */}
                            <button type="button" className="btn btn-theme ethereum-icon d-flex bg-gradient-theme" onClick={() => history.push({
                                pathname: `/song-detail/${props.nft?._id}`,
                                state: { autoPlay: true }
                            })}>

                                Free Play
                            </button>
                            {console.log("props", props.nft.owner._id == authUser._id)}
                            {<button type="button" className="btn btn-theme ethereum-icon d-flex bg-gradient-theme" disabled={"" + props.nft.owner._id == "" + authUser._id} onClick={() => {
                                if (props.nft?.original)
                                    setBulkBuyShow(true)
                                else
                                    setsmShows(true);
                            }} >
                                {props.nft?.currentPrice}
                            </button>}
                            {/* <button type="button" className="btn btn-theme ethereum-icon d-flex bg-gradient-theme"  >
                                View Detail
                            </button> */}
                        </div>
                    </div>
                </div>
            </div>
            <div className="modal-wrapper">
                <PayOrPlayPopup hideModel={hideModels} lgShows={lgShows} nft={props?.nft} />
                <CartPopup nftType="regular" hideModel={hideModel1} smShows={smShows} setBulkBuyShow={setBulkBuyShow} nft={props.nft} />
                <BulkBuyPopUp hideModel={hideModel2} bulkBuyShow={bulkBuyShow} setsmShows={setsmShows} nft={props.nft} />
            </div>
        </>
    )
}
export default DiscoverMusicNft;