import { UPSERT_IDENTITY,GET_IDENTITY,BEFORE_IDENTITY } from "../../../redux/types";

const initialState = {
    upsertIdentityAuth:false,
    GET_IDENTITY:false
}

export default function (state = initialState, action) {
    switch (action.type) {
        case UPSERT_IDENTITY:
            return {
                ...state,
                identity: action.payload,
                upsertIdentityAuth: true
            }
            case GET_IDENTITY:
            return {
                ...state,
                identity: action.payload,
                getIdentityAuth: true
            }
            case BEFORE_IDENTITY:
                return {
                    ...state,
                    getIdentityAuth:false,
                    upsertIdentityAuth: false
                }
        default:
            return {
                ...state
            }
    }
}