import React, { useEffect, useState } from "react";
import Header from '../Header/Header';
import imagePlaceholder from '../../images/image-placeholder.jpg';
import Form from 'react-bootstrap/Form';
import { connect } from 'react-redux';
import { ENV } from '../../config/config.js';
import FullPageLoader from '../FullPageLoader/FullPageLoader';
// import { updateProfile, beforeUser, ProfileUpdateFlag, getUser } from '../user/user.action';
import { updateProfile, beforeGetUser } from '../user/user.action';
import { signRequest } from "../../utils/web3";
import { toast } from 'react-toastify';

function EditDetails(props) {

    const [profile, setProfile] = useState();

    const [fullLoader, setFullLoader] = useState(true);

    useEffect(() => {
        const user = fetchUser();
        setProfile(user);
    }, [])

    useEffect(() => {
        if (props.user.getUserAuth) {
            const user = fetchUser();
            setProfile(user);
            props.beforeGetUser()
        }
    }, [props.user.getUserAuth])

    useEffect(() => {
        if (profile)
            setFullLoader(false);
    }, [profile])

    useEffect(() => {
        if (props.error)
            setFullLoader(false)
    }, [props.error])

    const fetchUser = () => {
        const user = ENV.getUserKeys('_id email username address profileImage description totalAmountUserInvesting nftsCount');
        for (let key in user) {
            if (user[key] === undefined)
                delete user[key];
        }
        return user;
    }

    const handleChange = (e) => {
        const { name, value } = e.target;
        const data = profile;
        data[name] = value;
        setProfile({ ...data })
    }
    const handleImageChange = (e) => {
        const file = e.target.files[0];
        if (!file) {
            const data = { ...profile };
            delete data[e.target.name];
            setProfile(data);
        }
        else if (checkMimeType(e)) {
            setProfile({ ...profile, [e.target.name]: file });
        }
        else {
            const data = { ...profile };
            delete data[e.target.name];
            setProfile(data);
            // toast.error("Some error occured while uploading photo. Please try again");
        }
    }

    const checkMimeType = (event) => {
        //getting file object
        let file = event.target.files;
        //define message container
        let err = "";
        // list allow mime type
        const types = ["image/png", "image/jpeg", "image/png", "image/jpg"];

        // compare file type find doesn't matach
        if (types.every((type) => file[0].type !== type)) {
            // create error message and assign to container
            err = file[0].type + " is not a supported format";
        }

        if (err !== "") {
            // if message not same old that mean has error
            event.target.value = null; // discard selected file
            toast.error(err);
            return false;
        }
        return true;
    };

    const handleRemovePhoto = () => {
        const data = { ...profile };
        if (data.profileImage)
            delete data.profileImage;
        if (data.image) {
            delete data.image;
            const element = document.getElementById("imageFileUpload");
            element.value = null;
        }
        setProfile(data);
    }

    const handleSubmit = async () => {
        const signature = await signRequest()
        const data = {
            ...profile, signature
        }

        var formData = new FormData()
        for (const key in data)
            formData.append(key, data[key])
        const element = document.getElementById("imageFileUpload")
        element.value = null
        props.updateProfile(formData)
        setFullLoader(true)
    }

    return (
        <>
            {fullLoader ? <FullPageLoader /> :
                <div className="edit-details-section">
                    <div className="profile-pic">
                        <div className="profile-pic-outer">
                            <div className="profile-pic-section">
                                {!profile?.image && <img className="img-fluid profilePhoto" src={profile.profileImage ? profile.profileImage : imagePlaceholder} alt="missing" />}
                                {profile?.image && <img className="img-fluid profilePhoto" src={URL.createObjectURL(profile.image)} alt="missing" />}
                            </div>
                            {(profile.profileImage || profile.image) && <div className="d-flex justify-content-center">
                                <button className="btn btn-danger remove-btn" onClick={handleRemovePhoto}>Remove</button>
                            </div>}
                        </div>
                        <Form.Group controlId="chooseImage" className="choose-pic mt-3 mb-5">
                            <Form.Label className="text-white">Change Profile Photo</Form.Label>
                            <Form.Control id="imageFileUpload" type="file" name="image" onChange={handleImageChange} className="mt-5" />
                        </Form.Group>
                    </div>
                    <div className="edit-form-section mt-5">
                        <Form.Group className="my-4 d-flex align-items-start basic-form" controlId="formBasicName">
                            <Form.Label className="text-white label-form">Name :</Form.Label>
                            <Form.Control type="email" placeholder="Enter Name" name="username" onChange={handleChange} defaultValue={profile.username} />
                        </Form.Group>
                        <Form.Group className="my-4 d-flex align-items-sm-center align-items-start basic-form" controlId="formBasicEmail">
                            <Form.Label className="text-white label-form">Email Address :</Form.Label>
                            <Form.Control type="email" placeholder="Enter Email" name="email" value={profile.email} disabled />
                        </Form.Group>
                        <Form.Group className="my-4 d-flex align-items-sm-center align-items-start basic-form" controlId="formBasicAddress">
                            <Form.Label className="text-white label-form">Account Address :</Form.Label>
                            <Form.Control type="email" placeholder="Enter Account Address" name="address" value={profile.address} disabled />
                        </Form.Group>
                        <Form.Group className="my-4 d-flex align-items-top basic-form" controlId="formBasicDescription">
                            <Form.Label className="text-white label-form">About Me :</Form.Label>
                            {/* <Form.Control type="text-area" placeholder="About My Self" name="description" onChange={handleChange} value={profile.description} /> */}
                            <textarea className="form-control" id="exampleFormControlTextarea1" name="description" rows="3" style={{ resize: "none" }} onChange={handleChange} value={profile.description}></textarea>
                        </Form.Group>
                    </div>
                    <div className="text-center">
                        <button className="mt-3 mt-sm-4 btn-white right-arrow" onClick={handleSubmit} >Save Changes</button>
                    </div>

                </div>
            }
        </>
    )
}

const mapStateToProps = (state) => (
    {
        user: state.user,
        error: state.error
    }
)
export default connect(mapStateToProps, { updateProfile, beforeGetUser })(EditDetails)