import React, { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import './Trending_slider.css';
import Slider from "react-slick";
import Trending_slide from "../Trending_slide/Trending_slide";

import { connect } from "react-redux";
import { getTrendingNfts, beforeTrendingNFT } from "./trendingNFTs.action";
import { useHistory } from "react-router-dom";
import FullPageLoader from "../FullPageLoader/FullPageLoader";

const settings = {
  dots: false,
  infinite: false,
  speed: 500,
  slidesToShow: 3,
  slidesToScroll: 1,
  arrows: false,
  responsive: [
    {
      breakpoint: 1399,
      settings: {
        slidesToShow: 3,
        slidesToScroll: 1
      }
    },
    {
      breakpoint: 1199,
      settings: {
        slidesToShow: 2,
        slidesToScroll: 1
      }
    },
    {
      breakpoint: 767,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1
      }
    }
  ]
};
function Trending_slider(props) {
  const history = useHistory();
  const [nfts, setNfts] = useState();
  const [loader, setLoader] = useState(true);

  useEffect(() => {
    props.getTrendingNfts()
  }, [])

  useEffect(() => {
    if (props.trendingNFTs.getTrendingNFTs) {
      setNfts(props.trendingNFTs.nfts);
      props.beforeTrendingNFT();
    }
  }, [props.trendingNFTs.getTrendingNFTs])

  useEffect(() => {
    if (nfts)
      setLoader(false);
  }, [nfts])

  useEffect(() => {
    if (props.error)
      setLoader(false)
  }, [props.error])


  return (
    <>
      {
        loader ?
          <FullPageLoader /> :
          <section className="trending-section">
            <Container>
              <Row className="margin-bottom">
                <Col md={6} xs={8}>
                  <h2 className="sub-heading">Trending</h2>
                </Col>
                {nfts && nfts?.length > 0 &&
                  <Col md={6} xs={4}>
                    <div className="float-end">
                      <button type="button" className="btn btn-theme bg-gradient-theme" onClick={() => history.push("/songs-and-projects")}>Show all</button>
                    </div>
                  </Col>}
              </Row>
              <Row className="trending-slider-section">
                <Col>
                  {nfts && (nfts?.length > 0) ?
                    <div className="home-page-slider">
                      <Slider className="music-group mt-2" {...settings}>
                        {nfts?.map((nft, index) => {
                          return <div key={index} className="music-list">
                            <Trending_slide nft={nft} />
                            {/* <Trending_slide /> */}
                          </div>

                        })}
                      </Slider>
                    </div>
                    :
                    <div className="home-slider-container">
                      <span className="text-danger">No data available</span>
                    </div>
                  }

                </Col>
              </Row>

            </Container>
          </section>
      }
    </>

  )
}
const mapStateToProps = (state) => ({
  trendingNFTs: state.trendingNFTs,
  error: state.error
})
export default connect(mapStateToProps, { getTrendingNfts, beforeTrendingNFT })(Trending_slider);