import React, { useEffect, useState } from "react";
import { Col, Container, Row, Dropdown } from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFilter } from '@fortawesome/free-solid-svg-icons';
import Button from "@restart/ui/esm/Button";
import DiscoverMusicNft from "../discover-nft/discover-nft";
import { getNFTs, beforeNFT } from '../ArtistInterface/Upload_create_modal/nft.action'
import { getUsers, beforeUser } from '../../components/user/user.action'
import { getProjects } from '../ArtistInterface/Create_project_modal/project.action'
import FullPageLoader from "../FullPageLoader/FullPageLoader";

import { connect } from 'react-redux'
import { ENV } from '../../config/config'
import axios from 'axios';
import Select from 'react-select'

const DiscoverMusic = (props) => {
    const [nfts, setNFTs] = useState();
    const [pagination, setPagination] = useState();
    const [rarities, setRarites] = useState();
    const [musicStyles, setMusicStyles] = useState([])
    const [albums, setAlbums] = useState([])
    const [dispalyFilters, setDisplayFilters] = useState(false)
    const [filterData, setFilterData] = useState({ songName: '', album: '', rarity: '', musicStyle: '', minPrice: '', maxPrice: '', creatorId: '', sort: '', priceType: '' })
    const [selectedCreator, setSelectedCreator] = useState({ label: "Select Creator", value: "" });
    const [selectedMusicStyle, setSelectedMusicStyle] = useState({ label: "Select Music Style", value: "" });
    const [selectedAlbum, setSelectedAlbum] = useState({ label: "Select Album", value: "" });
    const [selectedRarity, setSelectedRarity] = useState({ label: "Select Rarity", value: "" });
    const [selectedSort, setSelectedSort] = useState({ label: "Select Sort", value: "" });
    const [selectedPriceType, setSelectedPriceType] = useState({ label: "Select Price Type", value: "" });
    const [searchQuery, setSearchQuery] = useState('')
    const [showResetBtn, setShowResetBtn] = useState(false);
    const [creators, setCreators] = useState([]);
    const [fullLoader, setFullLoader] = useState(true);
    const sortOptions = [
        { label: "Recent", value: "recent" },
        { label: "Views", value: "views" },
        { label: "Likes", value: "likes" },
        { label: "Name", value: "name" },
        { label: "Price", value: "price" }
    ]
    const priceTypes = [
        { label: "Fixed Price", value: "fixed" },
        { label: "Auction", value: "auction" }
    ]

    useEffect(() => {
        props.getNFTs();
        props.getUsers('creators');
        fetchRarities();
        fetchAlbums();
        fetchMusicStyles();
    }, [])

    useEffect(() => {
        if (props.users.getUsersAuth) {
            let userOptions = []
            props.users.users.map((creator) => userOptions.push({ value: creator._id, label: creator.username }))
            setCreators(userOptions)
            // props.beforeUser()
        }
    }, [props.users.getUsersAuth])

    useEffect(() => {
        if (props.nfts.getNFTAuth) {
            const { nfts, pagination } = props.nfts;
            setNFTs(nfts);
            setPagination(pagination);
            props.beforeNFT();
        }
    }, [props.nfts.getNFTAuth])

    useEffect(() => {
        if (nfts && creators && albums && musicStyles)
            setFullLoader(false);
    }, [nfts, creators, albums, musicStyles])

    useEffect(() => {
        if (props.error)
            setFullLoader(false)
    }, [props.error])

    const fetchRarities = () => {
        const selectOptions = [];
        ENV.rarities.map(r => selectOptions.push({ label: r.name, value: r._id }));
        setRarites(selectOptions);
    }

    const fetchAlbums = async () => {
        const albumsData = await axios.get(ENV.url + 'album/list');
        const selectOptions = [];
        if (albumsData.data.success) {
            albumsData.data.data.albums.map(album => selectOptions.push({ value: album._id, label: album.name }));
            setAlbums(selectOptions);
        }
    }

    const fetchMusicStyles = async () => {
        const musicData = await axios.get(ENV.url + 'music-style/list');
        let selectOptions = [];
        if (musicData.data.success) {
            musicData.data.data.musicStyle.map(m => selectOptions.push({ value: m._id, label: m.name }));
            setMusicStyles(selectOptions);
        }
    }

    const handleLoadMore = () => {
        let qs = `page=${pagination?.page + 1}&limit=${9}&` + searchQuery;
        props.getNFTs(qs)
    }

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        let data = filterData
        setFilterData({ ...data, [name]: value })
    }

    const handleReactSelect = (selectedOption, e) => {
        const { name } = e;
        setFilterData({ ...filterData, [name]: selectedOption ? selectedOption.value : "" });
        if (name === "creatorId")
            setSelectedCreator(selectedOption ? selectedOption : { label: "Select Creator", value: "" })
        else if (name === "musicStyle")
            setSelectedMusicStyle(selectedOption ? selectedOption : { label: "Select Music Style", value: "" });
        else if (name === "album")
            setSelectedAlbum(selectedOption ? selectedOption : { label: "Select Album", value: "" });
        else if (name === "rarity")
            setSelectedRarity(selectedOption ? selectedOption : { label: "Select Rarity", value: "" });
        else if (name === "sort")
            setSelectedSort(selectedOption ? selectedOption : { label: "Select Sort", value: "" });
        else if (name === "priceType")
            setSelectedPriceType(selectedOption ? selectedOption : { label: "Select Price Type", value: "" });
    }

    const handleSearch = () => {
        let qs = ''
        for (const key in filterData) {
            setShowResetBtn(true)
            if (filterData[key] !== "")
                qs += `${key}=${filterData[key]}&`
        }
        setSearchQuery(qs);
        setFullLoader(true);
        props.getNFTs(qs);
    }

    const handleReset = async () => {
        setFullLoader(true)
        setFilterData({
            songName: '', album: '', rarity: '', musicStyle: '', minPrice: '', maxPrice: '', creatorId: '', sort: '', priceType: ''
        })
        setSelectedCreator({ label: "Select Creator", value: "" });
        setSelectedMusicStyle({ label: "Select Music Style", value: "" });
        setSelectedAlbum({ label: "Select Album", value: "" });
        setSelectedRarity({ label: "Select Rarity", value: "" });
        setSelectedSort({ label: "Select Sort", value: "" });
        setSelectedPriceType({ label: "Select Price Type", value: "" });
        setSearchQuery('');
        setShowResetBtn(false);
        props.getNFTs();
    }

    return (
        <>
            {
                fullLoader ? <FullPageLoader /> :
                    <div className="discover-music">
                        <Container>
                            <div className="filters-section-1 mb-4" style={{ backgroundColor: dispalyFilters ? "#DE05BA" : '' }} onClick={() => setDisplayFilters(!dispalyFilters)}>
                                <Row>
                                    <Col className="text-center">
                                        <span className="filter-icon-div">
                                            <FontAwesomeIcon icon={faFilter} className="filter-icon" style={{ color: dispalyFilters ? "#9000F3" : "#fff" }} />
                                        </span>
                                    </Col>
                                </Row>
                            </div>
                            <div style={{ display: dispalyFilters ? 'block' : 'none' }}
                                className="filters-section">

                                <div className="filter-body">
                                    <Row>
                                        <Col md={12} className="">
                                            <div className="discover-music-banner d-flex">
                                                <div className="search-input-group" >
                                                    <Select className="react-select-filter" name="creatorId" value={selectedCreator} isClearable={selectedCreator.value !== "" ? true : false} options={creators} placeholder="Select Creator" onChange={(selectedOption, e) => handleReactSelect(selectedOption, e)} />
                                                </div>
                                                <div className="search-input-group">
                                                    <Select className="react-select-filter" name="musicStyle" value={selectedMusicStyle} isClearable={selectedMusicStyle.value !== "" ? true : false} options={musicStyles} placeholder="Music Styles" onChange={(selectedOption, e) => handleReactSelect(selectedOption, e)} />
                                                </div>
                                                <div className="search-input-group">
                                                    <Select className="react-select-filter" name="album" value={selectedAlbum} isClearable={selectedAlbum.value !== "" ? true : false} options={albums} placeholder="Albums" onChange={(selectedOption, e) => handleReactSelect(selectedOption, e)} />
                                                </div>
                                            </div>
                                        </Col>
                                        <Col md={12} className="mt-3">
                                            <div className="discover-music-banner d-flex">
                                                <div className="search-input-group">
                                                    <Select className="react-select-filter" name="rarity" value={selectedRarity} isClearable={selectedRarity.value !== "" ? true : false} options={rarities} placeholder="Rarites" onChange={(selectedOption, e) => handleReactSelect(selectedOption, e)} />
                                                </div>
                                                <div className="search-input-group">
                                                    <Select className="react-select-filter" name="sort" value={selectedSort} isClearable={selectedSort.value !== "" ? true : false} options={sortOptions} placeholder="Sort By" onChange={(selectedOption, e) => handleReactSelect(selectedOption, e)} />
                                                </div>
                                                <div className="search-input-group" >
                                                    <Select className="react-select-filter" name="priceType" value={selectedPriceType} isClearable={selectedPriceType.value !== "" ? true : false} options={priceTypes} placeholder="Select Price Type" onChange={(selectedOption, e) => handleReactSelect(selectedOption, e)} />
                                                </div>
                                            </div>
                                        </Col>
                                        <Col md={12} className="mt-3">
                                            <div className="discover-music-banner d-flex">
                                                <div className="search-input-group">
                                                    <input type="text" name="songName" value={filterData.songName} onChange={handleInputChange} className="form-control" placeholder="Song Name" />
                                                </div>

                                                {/* Price */}
                                                <div className="search-input-group">
                                                    <input type="text" name="minPrice" value={filterData.minPrice} onChange={handleInputChange} className="form-control" placeholder="Min Price" />
                                                    {/* <span className="search-icon"><FontAwesomeIcon icon={faSearch} /></span> */}
                                                </div>
                                                <div className="search-input-group">
                                                    <input type="text" name="maxPrice" value={filterData.maxPrice} onChange={handleInputChange} className="form-control" placeholder="Max Price" />
                                                    {/* <span className="search-icon"><FontAwesomeIcon icon={faSearch} /></span> */}
                                                </div>

                                            </div>
                                        </Col>
                                        <Col md={12} className="mt-3">
                                            <div className="discover-music-banner d-flex justify-content-start">
                                                {/* {showResetBtn && <div className="search-button">
                                                    <Button className="btn reset-btn bg-gradient-theme" onClick={handleReset}>Reset</Button>
                                                </div>} */}
                                                <div className="search-button">
                                                    <Button className="btn reset-btn bg-gradient-theme" onClick={handleReset}>Reset</Button>
                                                </div>
                                                <div className="search-button">
                                                    <Button className="btn seacrh-btn" onClick={handleSearch}>Search</Button>
                                                </div>
                                            </div>
                                        </Col>
                                    </Row>
                                </div>
                            </div>
                            {(nfts && nfts.length > 0) ?
                                <Row className="discover-music-list">
                                    {nfts.map((nft, index) => {
                                        return (<Col xl={4} md={6} sm={12} key={index}>
                                            <div className="discover-list">
                                                <DiscoverMusicNft nft={nft} />
                                            </div>
                                        </Col>)
                                    })}
                                </Row> :
                                <Row className="mt-4">
                                    <Col>
                                        <div className="home-slider-container"><span className="text-danger">No Item Found</span></div>
                                    </Col>

                                    {/* <div className="text-danger text-center padding-top fw-700">No Item Found</div> */}
                                </Row>}

                            {pagination?.page < pagination?.pages &&
                                <Row>
                                    <Col md={12}>
                                        <div className="load-button text-center">
                                            <Button className="btn load-btn bg-gradient-theme" onClick={handleLoadMore}>Load More</Button>
                                        </div>
                                    </Col>
                                </Row>
                            }
                        </Container>
                    </div>
            }
        </>
    );
};

const mapStateToProps = (state) => ({
    nfts: state.nfts,
    projects: state.projects,
    users: state.user,
    error: state.error
})

export default connect(mapStateToProps, { getNFTs, getProjects, beforeNFT, getUsers, beforeUser })(DiscoverMusic);