import React, { useEffect, useState } from "react";
import { Col, Container, Row, Tabs, Tab } from "react-bootstrap";
import Header from "../Header/Header";
import LibraryTabs from "../LibraryTabs/LibraryTabs";
import MusicBox from '../MusicBox/MusicBox'
import './Library.css'
import { getRecentNfts } from '../../components/NFT_Slider/recentNFTs.action'
import { connect } from "react-redux";
import { ENV } from "../../config/config";

function Library(props) {
    const [userId, setUserId] = useState();
    useEffect(() => {
        let user = ENV.getUserKeys('_id')
        console.log('user', user)
        setUserId(user?._id);
        props.getRecentNfts()
    }, [])

    return (
        <div className="main-wrapper">
            {/* <Header /> */}
            <Container className="main-padding-top">
                <Row>
                    <Col md={12}>
                        <h1 className="title mb-5 text-center mt-5">Library</h1>
                    </Col>
                    <Col lg={6}>
                        <h2 className="sub-heading">Last buy</h2>
                        {!props?.nfts?.nfts.length && <Row>
                            <div className="home-slider-container">
                                <span className="text-danger">No data available</span>
                            </div>
                        </Row>}
                        <Row>
                            {props?.nfts?.nfts?.map((nft) => {
                                return (<MusicBox nft={nft} key={nft._id} />)
                            })}
                        </Row>
                    </Col>
                    <Col lg={6} className="my-lg-0 my-5">
                        {userId && <LibraryTabs userId={userId} />}
                    </Col>
                </Row>

            </Container>
        </div>
    )
}

const mapStateToProps = (state) => ({
    nfts: state.recentNFTs,
})

export default connect(mapStateToProps, { getRecentNfts })(Library);