import React, { useState, useEffect } from "react";
import { Col, Container, Row } from "react-bootstrap";
import Nft from '../ArtistInterface/NFT_Slider/Nft.js';
import Header from "../Header/Header";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlayCircle, faHeart, faPlay, faDotCircle, } from '@fortawesome/free-solid-svg-icons';
// import SongImg from '../../images/maxresdefault.jpg';
import Table from 'react-bootstrap/Table';
// import ReactAudioPlayer from 'react-audio-player';
import ReactPlayer from 'react-player';
// import { ReactAudio } from "reactjs-media";
import { useParams, useLocation, useHistory } from "react-router-dom";
// import useAudio from "./audio";
import { getNFT, beforeNFT, upsertNFT, likeNFT, dislikeNFT } from "../ArtistInterface/Upload_create_modal/nft.action.js";
import FullPageLoader from '../../components/FullPageLoader/FullPageLoader';
import imagePlaceholder from "../../images/image-placeholder.jpg";
import artistGreyImage from "../../images/artists.jpg";
import { connect } from "react-redux";
import { upsertCountSongStats } from '../songStats/songStats.action'
import { ENV } from "../../config/config.js";
import CartPopup from "../Cart/CartPopup.js";
import BulkBuyPopUp from "../bulkBuy/bulkBuy.js";
import SalePriceSettter from "../salePriceSetter/salePriceSetter.js";

// import AudioPlayer from 'react-h5-audio-player';
// import 'react-h5-audio-player/lib/styles.css';


const Song = (props) => {
    const { songId } = useParams();
    const [details, setDetails] = useState();
    const location = useLocation();
    const [playing, setPlaying] = useState(location?.state?.autoPlay ? true : false);
    const history = useHistory();
    const [loader, setLoader] = useState(true);
    const user = ENV.getUserKeys("_id");
    // console.log("user: ", user);
    const [smShows, setsmShows] = useState(false);
    const [bulkBuyShow, setBulkBuyShow] = useState(false);
    const [priceSetterShow, setPriceSetterShow] = useState(false);
    const hideModel1 = () => { setsmShows(false) };
    const hideModel2 = () => { setBulkBuyShow(false) };
    const hideModel3 = () => { setPriceSetterShow(false) };

    useEffect(() => {
        props.getNFT(songId);
    }, [songId])

    useEffect(() => {
        if (props.nfts.getNFTAuth) {
            setDetails(props.nfts.nft);
            props.beforeNFT();
        }
    }, [props.nfts.getNFTAuth])

    useEffect(() => {
        if (props.nfts.upsertNFTAuth || props.nfts.likeNFTAuth)
            props.getNFT(songId);
        hideModel3();
    }, [props.nfts.upsertNFTAuth, props.nfts.likeNFTAuth])

    useEffect(() => {
        if (details)
            setLoader(false);
    }, [details])

    useEffect(() => {
        if (props.error) {
            setLoader(false);
            hideModel3();
        }
    }, [props.error])

    // useEffect(() => {
    //     // if ((!details?.original) && (details?.owner._id === user._id) && (details?.status === 0))
    //     if ((!details?.original) && (details?.owner._id === user._id) && (details?.status === 0))
    //         console.log("condition true!")
    //     else
    //         console.log("condition false!");
    // })

    const handleLike = (like) => {
        if (!Object.keys(user).length) {
            history.push("/login");
            return null;
        }
        setLoader(true);
        if (like)
            props.likeNFT(details._id);
        else
            props.dislikeNFT(details._id);
    }

    const handlePaymentModel = () => {
        if (details?.original)
            setBulkBuyShow(true);
        else
            setsmShows(true);
    }

    const formattedAddress = (address) => {
        return `${address.slice(0, 5)}...${address.slice(-5)}`
    }

    return (
        <>
            {loader ?
                <FullPageLoader />
                :
                <div className="song-detail">
                    {/* <Header /> */}
                    <div className="about-song">
                        <Container>
                            <Row>
                                <Col md={8}>
                                    <div className="abt-song">
                                        <div className="song-img">
                                            <img className="img-fluid song-in-img" src={details?.image ? details.image : imagePlaceholder} alt="missing" />
                                        </div>
                                        <div className="song-audio">
                                            <div className="information-head text-center mt-5">
                                                <h2>Audio Clip</h2>
                                            </div>
                                            <div className="audio-clip text-center mt-5">
                                                <ReactPlayer
                                                    url={details?.music}
                                                    className="react-audio-player"
                                                    playing={playing}
                                                    controls={true}
                                                    // onStart={() => props.upsertCountSongStats('nfts/songCounts', JSON.stringify({ nftId: details._id }))}
                                                    onPause={() => setPlaying(false)}
                                                    onStart={() => {
                                                        if (!user._id) {
                                                            // history.push("/login")
                                                            history.push({
                                                                pathname: `/login`,
                                                                state: { url: window.location.pathname.split('/') }
                                                            })
                                                        }
                                                    }
                                                    }
                                                    onEnded={() => {
                                                        props.upsertCountSongStats('nfts/songCounts', JSON.stringify({ nftId: details._id }))
                                                    }}
                                                />
                                            </div>
                                        </div>
                                        <div className="song-information">
                                            <div className="information-head text-center mt-5">
                                                <h2>Song Information</h2>
                                            </div>
                                            <div className="song-all-info">
                                                <Row>
                                                    <Col md={12}>
                                                        <div className="basic-info d-flex align-items-center gap-3 mt-5">
                                                            <h3>Song Name: </h3>
                                                            <h4>{details?.name}</h4>
                                                        </div>
                                                    </Col>
                                                    {/* <Col md={12}>
                                                        <div className="basic-info d-flex align-items-center gap-3 mt-5">
                                                            <h3>Creator: </h3>
                                                            <h4>{details?.creator.username}</h4>
                                                        </div>
                                                    </Col>
                                                    <Col md={12}>
                                                        <div className="basic-info d-flex align-items-center gap-3 mt-5">
                                                            <h3>Owner: </h3>
                                                            <h4>{details?.owner.username}</h4>
                                                        </div>
                                                    </Col> */}
                                                    <Col md={12}>
                                                        <div className="basic-info d-flex align-items-center gap-3 mt-5">
                                                            <h3>Rarity: </h3>
                                                            {(details?.rarity && details.rarity === 1) ? <h4>Extremely rare</h4> : null}
                                                            {(details?.rarity && details.rarity === 2) ? <h4>Rare</h4> : null}
                                                            {(details?.rarity && details.rarity === 3) ? <h4>Popularity</h4> : null}
                                                        </div>
                                                    </Col>
                                                    <Col md={12}>
                                                        <div className="basic-info d-flex align-items-center gap-3 mt-5">
                                                            <h3>Music Style: </h3>
                                                            <h4>{details?.genre?.name}</h4>
                                                        </div>
                                                    </Col>
                                                    <Col md={12}>
                                                        <div className="basic-info d-flex align-items-top gap-3 mt-5">
                                                            <h3>Description: </h3>
                                                            <div className="description-section" dangerouslySetInnerHTML={{ __html: details?.description }}></div>
                                                        </div>
                                                    </Col>
                                                    <Col md={12}>
                                                        <div className="basic-info d-flex align-items-center gap-3 mt-5">
                                                            <h3>Price Type: </h3>
                                                            <h4>Auction</h4>
                                                        </div>
                                                    </Col>
                                                    <Col md={12}>
                                                        <div className="basic-info d-flex align-items-center gap-3 mt-5">
                                                            <h3>Price: </h3>
                                                            <h4>{details?.currentPrice} ETH</h4>
                                                        </div>
                                                    </Col>
                                                    <Col md={12}>
                                                        <div className="basic-info d-flex align-items-center gap-3 mt-5">
                                                            <h3>Quantity: </h3>
                                                            <h4>{details?.artistCopies}</h4>
                                                        </div>
                                                    </Col>
                                                </Row>
                                            </div>
                                        </div>
                                    </div>
                                </Col>
                                <Col md={4}>
                                    <div className="pro-detail">
                                        <h3>
                                            {details?.name}
                                        </h3>
                                    </div>
                                    <div className="d-flex icons-row justify-content-start">
                                        <div className="like">
                                            <FontAwesomeIcon className="text-white" icon={faHeart} />
                                            <span>{details?.likes}</span>
                                        </div>
                                        <div className="view">
                                            <FontAwesomeIcon className="text-white" icon={faPlay} />
                                            <span>{details?.timesPlayed}</span>
                                        </div>
                                        {details?.track && details?.totalTracks && <div className="subtract">
                                            <span><FontAwesomeIcon className="text-white" icon={faDotCircle} /></span>
                                            {details ? <span>{details.track} / {details.totalTracks}</span> : null}
                                        </div>}
                                    </div>
                                    <div className="song-button d-flex gap-3">
                                        {/* <button type="button" className="btn btn-theme banner-btn bg-gradient">Participate</button> */}
                                        <button type="button" className="btn btn-theme ethereum-icon d-flex bg-gradient-theme" onClick={() => setPlaying(true)}>
                                            Free - Play
                                        </button>
                                        {!details?.liked && <button type="button" className="btn btn-theme ethereum-icon d-flex bg-gradient-theme" onClick={() => handleLike(true)}>
                                            Like
                                        </button>}
                                        {details?.liked && <button type="button" className="btn btn-theme ethereum-icon d-flex bg-gradient-theme" onClick={() => handleLike(false)}>
                                            Dislike
                                        </button>}
                                        {(!details?.original) && (details?.owner._id === user._id) && (details?.status === 0) && <button type="button" className="btn btn-theme ethereum-icon d-flex bg-gradient-theme" onClick={() => {
                                            if (user._id)
                                                setPriceSetterShow(true);
                                            else
                                                history.push("/login");
                                        }}>
                                            Put on Sale
                                        </button>}
                                        {details?.owner._id !== user._id && (details?.status !== 0) && <button type="button" className="btn btn-theme ethereum-icon d-flex bg-gradient-theme" onClick={() => {
                                            if (user._id)
                                                handlePaymentModel();
                                            else
                                                history.push("/login");
                                        }} >
                                            {details?.currentPrice} ETH
                                        </button>}

                                    </div>
                                    <div className="song-cont">
                                        <div className="song-person-img">
                                            <img className="img-fluid person-in-img" src={details?.owner.profilePhoto ? details.owner.profilePhoto : artistGreyImage} alt="missing" />
                                        </div>
                                        <h3>
                                            {/* About <br /> */}
                                            Owner:  {details?.owner.username}
                                        </h3>
                                        <div>
                                            <p className="description-section" dangerouslySetInnerHTML={{ __html: details?.owner?.description }}></p>
                                        </div>
                                    </div>
                                    <div className="song-cont">
                                        <div className="song-person-img">
                                            <img className="img-fluid person-in-img" src={details?.creator.profilePhoto ? details.creator.profilePhoto : artistGreyImage} alt="missing" />
                                        </div>
                                        <h3>
                                            {/* About <br /> */}
                                            Creator: {details?.creator.username}
                                        </h3>
                                        <div>
                                            <p className="description-section" dangerouslySetInnerHTML={{ __html: details?.creator?.description }}></p>
                                        </div>
                                    </div>
                                </Col>
                            </Row>
                            <Row className="song-padding">
                                <Col md={12}>
                                    <div className="basic-info text-center">
                                        <h3>ROYALTIES</h3>
                                    </div>
                                </Col>
                                {details?.royalties && details?.royalties.length > 0 && <Col md={12}>
                                    <Table striped bordered hover variant="dark" className="royalty-table">
                                        <thead>
                                            <tr>
                                                <th>Percentage</th>
                                                <th>Type</th>
                                                <th>Wallet</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {details?.royalties?.map((royalty, index) => {
                                                return (<tr key={index}>
                                                    <td>{royalty.percent}</td>
                                                    <td>{royalty.type}</td>
                                                    <td>{royalty.address && formattedAddress(royalty.address)}</td>

                                                </tr>)
                                            })}
                                        </tbody>
                                    </Table>
                                </Col>}
                                {
                                    (!details?.royalties.length > 0) && <div>NO Royalty Exist</div>
                                }
                            </Row>
                        </Container>
                    </div>
                    <div className="top-section">

                        < Nft />
                    </div>
                </div >
            }
            {details && <div className="modal-wrapper">
                <CartPopup nftType="single" hideModel={hideModel1} smShows={smShows} setBulkBuyShow={setBulkBuyShow} nft={details} />
                <BulkBuyPopUp hideModel={hideModel2} bulkBuyShow={bulkBuyShow} setsmShows={setsmShows} nft={details} />
                <SalePriceSettter hideModel={hideModel3} priceSetterShow={priceSetterShow} update={props.upsertNFT} nftId={details._id} />
            </div>}
        </>
    );
};

const mapStateToProps = (state) => ({
    nfts: state.nfts,
    error: state.error
})

export default connect(mapStateToProps, { getNFT, beforeNFT, upsertNFT, likeNFT, dislikeNFT, upsertCountSongStats })(Song);
