export const getSingerDetails = (nftId = '', qs = null) => dispatch => {
    dispatch(emptyError());
    let url = `${ENV.url}users/get-artist/${nftId}`;

    if (qs)
        url += `?${qs}`

    fetch(url, {
        method: 'GET',
        headers: {
            'content-type': 'application/json',
            'Authorization': ENV.Authorization,
            'x-auth-token': ENV.x_auth_token
        }
    }).then(res => res.json()).then(data => {
        if (data.success) {
            // toast.success(data.message)
            dispatch({
                type: GET_SINGER_DETAILS,
                payload: data.singer
            })
        } else {
            dispatch({
                type: GET_ERRORS,
                payload: data
            })
        }
    }).catch(error => {
        if (error.response && error.response.data) {
            const { data } = error.response
            // if (data.message)
            //     toast.error(data.message)
        }
        dispatch({
            type: GET_ERRORS,
            payload: error
        })
    })
};

export const getArtistDashInfo = (qs = null) => dispatch => {
    dispatch(emptyError());
    const user = ENV.getUserKeys('_id');
    let url = `${ENV.url}users/get-artist-dash-info/${user._id}`;

    if (qs)
        url += `?${qs}`

    fetch(url, {
        method: 'GET',
        headers: {
            'content-type': 'application/json',
            'Authorization': ENV.Authorization,
            'x-auth-token': ENV.x_auth_token
        }
    }).then(res => res.json()).then(data => {
        if (data.success) {
            // toast.success(data.message)
            dispatch({
                type: GET_SINGER_DASH_INFO,
                payload: data.data
            })
        } else {
            dispatch({
                type: GET_ERRORS,
                payload: data
            })
        }
    }).catch(error => {
        if (error.response && error.response.data) {
            const { data } = error.response
            // if (data.message)
            //     toast.error(data.message)
        }
        dispatch({
            type: GET_ERRORS,
            payload: error
        })
    })
};

export const getSingers = (author, qs = '') => dispatch => {
    dispatch(emptyError());
    let url = `${ENV.url}users/${author}`;
    if (qs)
        url += `?${qs}`

    fetch(url, {
        method: 'GET',
        headers: {
            'content-type': 'application/json',
            'Authorization': ENV.Authorization,
            'x-auth-token': ENV.x_auth_token
        }
    }).then(res => res.json()).then(data => {
        if (data.success) {
            // if (!qs)
            //     toast.success(data.message)
            dispatch({
                type: GET_SINGERS,
                payload: data.data
            })
        } else {
            dispatch({
                type: GET_ERRORS,
                payload: data
            })
        }
    }).catch(error => {
        if (error.response && error.response.data) {
            const { data } = error.response
        }
        dispatch({
            type: GET_ERRORS,
            payload: error
        })
    })
};

export const addVisitor = (id) => {
    let url = `${ENV.url}users/add-visit`;
    console.log("visitorId: ", id);
    fetch(url, {
        method: 'PUT',
        headers: {
            'content-type': 'application/json',
            'Authorization': ENV.Authorization,
            'x-auth-token': ENV.x_auth_token,
            // 'x-access-token': ENV.getUserKeys('accessToken') && ENV.getUserKeys('accessToken').accessToken ? ENV.getUserKeys('accessToken').accessToken : ''
        },
        body: JSON.stringify({ artist: id })
    }).then(res => res.json()).then(data => {
        if (data.success) {
            console.log("visitor added: ", data.data);
        } else {
            console.log(data.message);
        }
    }).catch(error => {
        if (error.response && error.response.data) {
            const { data } = error.response
            console.log("error while adding visitor: ", data);
        }
    })
};


