import { UPSERT_PROJECT, GET_PROJECT, GET_PROJECTS, BEFORE_PROJECT } from "../../../redux/types";

const initialState = {
    upsertProjectAuth: false,
    getProjectAuth: false,
    getProjectsAuth: false,
    projects: [],
    project: null,
}

export default function (state = initialState, action) {
    switch (action.type) {
        case UPSERT_PROJECT:
            return {
                ...state,
                project: action.payload,
                upsertProjectAuth: true
            }
        case GET_PROJECTS:
            return {
                ...state,
                // projects: [...state.projects.projects, ...action.payload.projects],
                // projects: action.payload.projects,
                projects: [...state.projects, ...action.payload.projects],
                pagination: action.payload.pagination,
                getProjectsAuth: true
            }
        case GET_PROJECT:
            return {
                ...state,
                project: action.payload,
                getProjectAuth: true
            }
        case BEFORE_PROJECT:
            return {
                ...state,
                getProjectAuth: false,
                projects: [],
                project: null,
                upsertProjectAuth: false,
                getProjectsAuth: false
            }
        default:
            return {
                ...state
            }
    }
}