import { UPSERT_NFT, BEFORE_NFT, GET_ERRORS, DELETE_NFT, GET_NFTS, GET_POP_NFTS, GET_NFT, LIKE_NFT, PURCHASING_NFT, SET_QUANTITIES } from "../../../redux/types";

const initialState = {
    nft: null,
    nfts: [],
    pagination: null,
    deleteNFTAuth: false,
    upsertNFTAuth: false,
    likeNFTAuth: false,
    purchasingNFTAuth: false,
    getNFTAuth: false,
    quantities: {
        freeCopies: 0,
        artistCopies: 0,
        // sellerCopies: 0,
    },
    // prices: {
    //     artistCopies: 0
    // }
}

export default function (state = initialState, action) {
    switch (action.type) {
        case UPSERT_NFT:
            return {
                ...state,
                nft: action.payload,
                upsertNFTAuth: true
            }
        case DELETE_NFT:
            return {
                ...state,
                nft: action.payload,
                deleteNFTAuth: true
            }
        case GET_NFTS:
            // projects: [...state.projects, ...action.payload.projects],
            // pagination: action.payload.pagination,
            return {
                ...state,
                nfts: action.payload.nfts,
                // projects: [...state.nfts, ...action.payload.nfts],
                pagination: action.payload.pagination,
                getNFTAuth: true
            }
        case GET_POP_NFTS:
            return {
                ...state,
                nfts: action.payload.nfts,
                getNFTAuth: true
            }

        case GET_NFT:
            return {
                ...state,
                nft: action.payload,
                getNFTAuth: true
            }
        case LIKE_NFT:
            return {
                ...state,
                nft: action.payload,
                likeNFTAuth: true
            }
        case PURCHASING_NFT:
            return {
                ...state,
                nft: action.payload,
                purchasingNFTAuth: true
            }
        case SET_QUANTITIES:
            return {
                ...state,
                quantities: action.payload
            }
        case BEFORE_NFT:
            return {
                ...state,
                nft: null,
                nfts: [],
                pagination: null,
                deleteNFTAuth: false,
                upsertNFTAuth: false,
                likeNFTAuth: false,
                purchasingNFTAuth: false,
                getNFTAuth: false,
                quantities: {
                    freeCopies: 0,
                    artistCopies: 0,
                    // sellerCopies: 0,
                },
                // prices: {
                //     artistCopies: 0
                // }
            }
        default:
            return {
                ...state
            }
    }
}