import { GET_NFT_TRANSACTIONS, GET_PROJECT_TRANSACTIONS, GET_TRANSACTION, BEFORE_TRANSACTION, GET_SELLING_STATS } from "../../redux/types";

const initialState = {
    nftTransactions: [],
    projectTransactions: [],
    pagination1: null,
    pagination2: null,
    transaction: null,
    getTransactionAuth: false,
    getNFTTransactionAuth: false,
    getProjectTransactionAuth: false,
    sellingStats: null,
    sellingStatsAuth: false
}

export default function (state = initialState, action) {
    switch (action.type) {
        case GET_NFT_TRANSACTIONS:
            return {
                ...state,
                nftTransactions: action.payload.transactions,
                pagination1: action.payload.pagination,
                getNFTTransactionAuth: true
            }

        case GET_PROJECT_TRANSACTIONS:
            return {
                ...state,
                projectTransactions: action.payload.transactions,
                pagination2: action.payload.pagination,
                getProjectTransactionAuth: true
            }

        case GET_TRANSACTION:
            return {
                ...state,
                payment: action.payload,
                getTransactionAuth: true
            }

        case GET_SELLING_STATS:
            return {
                ...state,
                sellingStats: action.payload,
                sellingStatsAuth: true
            }

        case BEFORE_TRANSACTION:
            return {
                ...state,
                nftTransactions: [],
                projectTransactions: [],
                pagination1: null,
                pagination2: null,
                transaction: null,
                getTransactionAuth: false,
                getNFTTransactionAuth: false,
                getProjectTransactionAuth: false,
                sellingStats: [],
                sellingStatsAuth: false
            }
        default:
            return {
                ...state
            }
    }
}