import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import { setQuantities } from '../ArtistInterface/Upload_create_modal/nft.action';
import { Modal } from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes, faPlus, faMinus } from '@fortawesome/free-solid-svg-icons';
import { toast } from 'react-toastify';
import './bulkBuy.css';

const BulkBuyPopUp = ({ hideModel, bulkBuyShow, setsmShows, nft, ...props }) => {

    const [prices, setPrices] = useState({
        artistCopies: 0
    });

    useEffect(() => {
        setPrices({ ...prices, artistCopies: nft.currentPrice * props.quantities.artistCopies });
    }, [props.quantities])

    const onIncrement = (value, name) => {
        if (props.quantities[name] + value < 0) return alert(`${name} can't be decreased more`);
        if (name === "freeCopies" && (props.quantities[name] + value) > nft.freeCopy)
            return alert(`${name} can't be increased more`);
        if (name === "artistCopies" && (props.quantities[name] + value) > nft.artistCopies)
            return alert(`${name} can't be increased more`);
        props.setQuantities({ ...props.quantities, [name]: props.quantities[name] + value });
    }

    return (
        <>
            <Modal
                className="bulk-buy-popup"
                show={bulkBuyShow}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Body>
                    <div className="bulk-buy-content">
                        <div className="cross-btn float-end" onClick={() => {
                            hideModel()
                            props.setQuantities({
                                freeCopies: 0,
                                artistCopies: 0,
                                // sellerCopies: 0,
                            });
                        }}>
                            <FontAwesomeIcon icon={faTimes} />
                        </div>
                        <h2 className="sub-heading mb-4">Buying Options</h2>
                        <div className="cart-details pt-3">
                            <div className="row justify-content-md-between my-4">
                                <div className="col-md-5">
                                    <h4>Free</h4>
                                    <span className={`option-desc${!nft.freeCopy ? " dim" : ""}`}>{nft.freeCopy ? nft.freeCopy + " versions available" : "No more available"}</span>
                                </div>
                                <div className="col-md-4">
                                    <h4 className={`option-price${!nft.freeCopy ? " dim" : ""}`}> 0 ETH</h4>
                                </div>
                                <div className="col-md-3">
                                    <div className="quantity d-flex justify-content-end">
                                        <span><FontAwesomeIcon icon={faMinus} onClick={() => onIncrement(-1, "freeCopies")} /></span>
                                        <input type="text" name="freeCopies" value={props.quantities.freeCopies} readOnly />
                                        <span><FontAwesomeIcon icon={faPlus} onClick={() => onIncrement(1, "freeCopies")} /></span>
                                    </div>
                                </div>
                            </div>
                            <div className="row justify-content-md-between my-4" >
                                <div className="col-md-5">
                                    <h4>From artist</h4>
                                    <span className={`option-desc${!nft.artistCopies ? " dim" : ""}`}>{nft.artistCopies ? nft.artistCopies + " versions available" : "No more available"}</span>
                                </div>
                                <div className="col-md-4">
                                    <h4 className={`option-price${!nft.artistCopies ? " dim" : ""}`}><span className="price-from-artist me-2">{prices.artistCopies}</span> ETH</h4>
                                </div>
                                <div className="col-md-3">
                                    <div className="quantity d-flex justify-content-end">
                                        <span><FontAwesomeIcon icon={faMinus} onClick={() => onIncrement(-1, "artistCopies")} /></span>
                                        <input type="text" name="artistCopies" value={props.quantities.artistCopies} />
                                        <span><FontAwesomeIcon icon={faPlus} onClick={() => onIncrement(1, "artistCopies")} /></span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="d-flex justify-content-center">
                            <button className="btn btn-theme d-flex bg-gradient-theme participate-button" onClick={() => {
                                if ((props.quantities.freeCopies + props.quantities.artistCopies) > 0) {
                                    hideModel();
                                    setsmShows(true);
                                }
                                else
                                    toast.error("There should be atleast 1 quantity to sell")
                                // alert("There should be atleast 1 quantity to sell");
                            }}>Next</button>
                        </div>
                    </div>
                </Modal.Body>
            </Modal>
        </>
    )
}

const mapStateToProps = (state) => ({
    quantities: state.nfts.quantities,
    error: state.error,
})

export default connect(mapStateToProps, { setQuantities })(BulkBuyPopUp);