import { GET_PROJECT_TRANSACTIONS, GET_NFT_TRANSACTIONS, GET_TRANSACTION, BEFORE_TRANSACTION, GET_ERRORS, GET_SELLING_STATS } from "../../redux/types";
import { ENV } from '../../config/config';

import { emptyError } from "../../redux/shared/error/error.action";

export const getTransaction = (id) => dispatch => {
    dispatch(emptyError());
    let url = `${ENV.url}/transaction/nft/get/${id}`;
    // if (qs)
    //     url += `?${qs}`

    fetch(url, {
        method: 'GET',
        headers: {
            'content-type': 'application/json',
            'Authorization': ENV.Authorization,
            'x-auth-token': ENV.x_auth_token
        }
    }).then(res => res.json()).then(data => {
        if (data.success) {
            // if (!qs)
            //     toast.success(data.message)

            // console.log("Dispatch")
            dispatch({
                type: GET_TRANSACTION,
                payload: data.data
            })
        } else {
            // if (!qs)
            //     toast.error(data.message)
            dispatch({
                type: GET_ERRORS,
                payload: data
            })
        }
    }).catch(error => {
        if (error.response && error.response.data) {
            const { data } = error.response
            // if (data.message)
            // toast.error(data.message)
        }
        dispatch({
            type: GET_ERRORS,
            payload: error
        })
    })
};

