import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import { Col, Container, Row, Tab, Tabs } from "react-bootstrap";
import Table from 'react-bootstrap/Table';
import { getNFTTransactions, getProjectTransactions, beforeTransaction } from "./paymentHistory.action";
import FullPageLoader from '../../components/FullPageLoader/FullPageLoader';
import { faLink } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Pagination from 'rc-pagination';
import 'rc-pagination/assets/index.css';
import { Link } from "react-router-dom";
import { ENV } from "../../config/config";

const PaymentHistory = (props) => {
    const [nftPayments, setNFTPayments] = useState();
    const [projectPayments, setProjectPayments] = useState();
    const [pagination1, setPagination1] = useState();
    const [pagination2, setPagination2] = useState();
    const [loader, setLoader] = useState(true);

    useEffect(() => {
        props.getNFTTransactions();
        props.getProjectTransactions();
    }, [])

    useEffect(() => {
        if (props.payments.getNFTTransactionAuth) {
            const { nftTransactions, pagination1 } = props.payments;
            setNFTPayments(nftTransactions);
            setPagination1(pagination1)
        }
    }, [props.payments.getNFTTransactionAuth])

    useEffect(() => {
        if (props.payments.getProjectTransactionAuth) {
            const { projectTransactions, pagination2 } = props.payments;
            setProjectPayments(projectTransactions);
            setPagination2(pagination2);
        }
    }, [props.payments.getProjectTransactionAuth])

    useEffect(() => {
        if (projectPayments && nftPayments) {
            props.beforeTransaction();
            setLoader(false);
        }
    }, [projectPayments, nftPayments])

    useEffect(() => {
        if (props.error)
            setLoader(false)
    }, [props.error])

    const onPageChange1 = async (page) => {
        setLoader(true)
        const qs = ENV.objectToQueryString({ page })
        props.getNFTTransactions(qs);
    }

    const onPageChange2 = async (page) => {
        setLoader(true)
        const qs = ENV.objectToQueryString({ page })
        props.getProjectTransactions(qs);
    }

    return (
        <>
            {loader ?
                <FullPageLoader />
                :

                <div className="songs-projects">
                    <Container>
                        <Row>
                            <Col md={12} xs={12}>
                                <div className="d-flex gap-3 justify-content-center">
                                    <span className="sp-head purple">Payment</span>
                                    {/* <span className="and-font">&amp;</span> */}
                                    <span className="sp-head yellow">History</span>
                                </div>
                            </Col>
                        </Row>
                        <Row className="mt-5">
                            <Col md={12} sx={12}>
                                <Tabs defaultActiveKey="songs" id="uncontrolled-tab-example" className="mb-3">
                                    <Tab eventKey="songs" title="SONGS">
                                        <div className="songs-table mt-5">
                                            <Table striped bordered variant="dark">
                                                <thead>
                                                    <tr>
                                                        <th>#</th>
                                                        <th>Seller Id</th>
                                                        <th>Buyer Id</th>
                                                        <th>Nft</th>
                                                        <th>Transactional Amount</th>
                                                        <th>Transactional Hash</th>
                                                        <th>Actions</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {nftPayments && nftPayments.map((p, index) => {
                                                        return (
                                                            <tr key={index}>
                                                                <td>{pagination1 && ((pagination1.limit * pagination1.page) - pagination1.limit) + index + 1}</td>
                                                                <td>{p?.sellerId}</td>
                                                                <td>{p?.buyerId}</td>
                                                                <td>{p?.nft}</td>
                                                                <td>{p?.transactionalAmount}</td>
                                                                <td>{p?.transactionalHash}</td>
                                                                <td className="text-center"><a className="text-white link-icon" target="_blank" href={`${ENV.transactionBaseUrl}${p?.transactionalHash}`}><FontAwesomeIcon icon={faLink} /></a></td>
                                                            </tr>)
                                                    })}
                                                </tbody>
                                            </Table>
                                            {pagination1 && <div className="table-pagination mt-5">
                                                <Pagination
                                                    className="m-3"
                                                    defaultCurrent={1}
                                                    pageSize // items per page
                                                    current={pagination1.page} // current active page
                                                    total={pagination1.pages} // total pages
                                                    onChange={onPageChange1}
                                                />
                                            </div>}
                                        </div>
                                    </Tab>
                                    <Tab eventKey="projects" title="PROJECTS">
                                        <div className="songs-table mt-5">
                                            <Table striped bordered variant="dark">
                                                <thead>
                                                    <tr>
                                                        <th>#</th>
                                                        {/* <th>Type</th> */}
                                                        <th>Amount</th>
                                                        {/* <th>Address</th> */}
                                                        {/* <th>Network</th> */}
                                                        <th>Project Id</th>
                                                        <th>User Id</th>
                                                        <th>Actions</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {projectPayments && projectPayments.map((p, index) => {
                                                        return (
                                                            <tr key={index}>
                                                                <td>{pagination2 && ((pagination2.limit * pagination2.page) - pagination2.limit) + index + 1}</td>
                                                                {/* <td>{p?.type}</td> */}
                                                                <td>{p?.amount}</td>
                                                                {/* <td>{p?.address}</td> */}
                                                                {/* <td>{p?.network}</td> */}
                                                                <td>{p?.project}</td>
                                                                <td>{p?.user}</td>
                                                                <td className="text-center"><a className="text-white link-icon" target="_blank" href={`${ENV.transactionBaseUrl}${p?.transactionalHash}`}><FontAwesomeIcon icon={faLink} /></a></td>
                                                            </tr>
                                                        )
                                                    })}
                                                </tbody>
                                            </Table>
                                            {pagination2 && <div className="table-pagination mt-5">
                                                <Pagination
                                                    className="m-3"
                                                    defaultCurrent={1}
                                                    pageSize // items per page
                                                    current={pagination2.page} // current active page
                                                    total={pagination2.pages} // total pages
                                                    onChange={onPageChange2}
                                                />

                                            </div>}
                                        </div>
                                    </Tab>
                                </Tabs>

                            </Col>
                        </Row>

                    </Container>
                </div >

            }
        </>
    );
};

const mapStateToProps = (state) => ({
    payments: state.payments,
    error: state.error
})

export default connect(mapStateToProps, { getNFTTransactions, getProjectTransactions, beforeTransaction })(PaymentHistory);
