import React, { useState, useEffect } from "react";
import { Modal } from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import Trending_slide from "../Trending_slide/Trending_slide";
import FullPageLoader from "../FullPageLoader/FullPageLoader";
import { connect } from "react-redux";
import { upsertParticipant, beforeParticipant } from './participant.action'
import { getPopProject } from '../ArtistInterface/Create_project_modal/project.action'
import { fundProject } from './../../utils/web3';
import { ENV } from '../../config/config'
// import '../Cart.css'
import { getBalance } from '../../utils/web3';
import { useHistory } from "react-router-dom";



function ParticipateModal(props) {
    // const [lgShow, setLgShow] = useState(false);

    // const hideModel = () => { setLgShow(false) }

    const [WalletBalance, setWalletBalance] = useState(0);
    const history = useHistory()
    const [participation, setParticipation] = useState({
        amount: '',
        project: ''
    })
    const [error, setError] = useState({})
    const [fullLoader, setFullLoader] = useState(false)

    useEffect(() => {
        fetchWalletBalance();
    }, [])

    useEffect(() => {
        setParticipation({ ...participation, project: props.projectId, projectChainId: props.projectChainId })
    }, [props.projectId])
    useEffect(() => {
        if (props?.error)
            setFullLoader(false)
    }, [props?.error])

    useEffect(() => {
        if (props.participants.upsertParticipantAuth) {
            props.hideModel()
            setFullLoader(false);
            props.beforeParticipant()
            if (props.updateDashboard) {
                props.getPopProject()
            }
        }
    }, [props.participants.upsertParticipantAuth])

    const fetchWalletBalance = async () => {
        let balance = await getBalance();
        setWalletBalance(balance);
    }

    const handleChange = (e) => {
        const { name, value } = e.target;
        const data = participation;
        data[name] = value;
        setParticipation({ ...data })

        let errorObj = error;
        delete errorObj[e.target.name]
        setError({ ...errorObj })
    }

    const handleSubmit = async () => {
        const isValidate = validate()
        if (!isValidate) return;
        const user = ENV.getUserKeys('_id');
        if (!Object.keys(user).length) {
            history.push("/login")
            return;
        }

        setFullLoader(true);
        let fundProjectRes = await fundProject(participation.projectChainId, participation.amount);
        console.log('CREATE PROJECT RESPONSE', fundProjectRes)

        if (fundProjectRes) {
            //Append intoform Data 
            var formData = new FormData()
            for (const key in participation)
                formData.append(key, participation[key])
            await props.upsertParticipant('project/participate', formData);
            setFullLoader(false);
        } else {
            setFullLoader(false);
        }
    }

    const validate = () => {
        if (participation.amount == '') {
            error.amount = "Amount is Required"
        }
        if (typeof participation.amount == "number") {
            error.amount = "Amount  must be  number"
        }
        if (participation.amount < 0) {
            error.amount = "Please put greater than 0"
        }
        if (Object.keys(error).length === 0) {
            return true;
        } else {
            setError({ ...error })
            return false;
        }
    }
    return (
        <>
            {fullLoader ? <FullPageLoader /> : ""}

            <Modal
                className="cart-popup bg-pink"
                show={props.smShows}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Body>
                    <div className="cart-content">
                        <div className="cross-btn float-end" onClick={() => props.hideModel()}>
                            <FontAwesomeIcon icon={faTimes} />
                        </div>
                        <h2 className="sub-heading mb-4">Pay</h2>
                        {/* <Trending_slide /> */}
                        <div className="cart-details pt-3">
                            <div className="pt-4" >
                                <div className="d-flex justify-content-between ">
                                    <span className="price">Price</span>
                                    {/* <span className="ethereum-icon ethrum-rate d-flex">0,09 ETH</span> */}
                                    <input type="number" name="amount" onChange={handleChange} className="participate-input" /> (ETH)
                                </div>
                                <div className="mt-2">
                                    {error?.amount && <p className="text-red">{error?.amount}</p>}
                                </div>
                            </div>
                            <div className="d-flex justify-content-between pt-4" >
                                <span className="price">My wallet</span>
                                <span className="ethereum-icon ethrum-rate d-flex">
                                    {WalletBalance}
                                </span>
                                {/* <input type="number" name="amount" onChange={handleChange}/> */}
                            </div>
                            <div className="text-center mt-5 mb-2">
                                {/* <button type="button" className="btn btn-theme cart-button bg-white">
                                    <svg width="18" height="19" viewBox="0 0 17 18" xmlns="http://www.w3.org/2000/svg">
                                        <g clip-path="url(#clip0)">
                                            <path d="M2.125 9.53125L8.5 17.5L14.875 9.53125L8.5 12.1875L2.125 9.53125ZM5.54726 12.1082L8.09138 13.1683L8.5 13.3385V15.7992L5.54726 12.1082Z" fill="url(#paint0_linear)" />
                                            <path d="M14.875 9.00002L8.5 0.5L2.125 9.00002L8.5 11.125L14.875 9.00002ZM8.5 7.93751L3.86362 8.45269L8.5 2.27077V7.93751Z" fill="url(#paint1_linear)" />
                                        </g>
                                        <defs>
                                            <linearGradient id="paint0_linear" x1="14.5236" y1="9.53125" x2="1.17984" y2="11.7092" gradientUnits="userSpaceOnUse">
                                                <stop stop-color="#EA06B1" />
                                                <stop offset="1" stop-color="#8200FE" />
                                            </linearGradient>
                                            <linearGradient id="paint1_linear" x1="14.5236" y1="0.499999" x2="1.02661" y2="2.15222" gradientUnits="userSpaceOnUse">
                                                <stop stop-color="#EA06B1" />
                                                <stop offset="1" stop-color="#8200FE" />
                                            </linearGradient>
                                            <clipPath id="clip0">
                                                <rect width="17" height="17" fill="white" transform="translate(0 0.5)" />
                                            </clipPath>
                                        </defs>
                                    </svg>
                                    <span onClick={handleSubmit}>Confirm &amp; pay</span>
                                </button> */}

                                <button type="button" className="btn btn-theme ethereum-icon d-flex bg-gradient-theme participate-button">
                                    <span onClick={handleSubmit}>Confirm &amp; pay</span>
                                </button>
                            </div>
                        </div>
                    </div>
                </Modal.Body>
            </Modal>

        </>
    )
}

const mapStateToProps = (state) => ({
    participants: state.participants,
    error: state.error
})
export default connect(mapStateToProps, { upsertParticipant, beforeParticipant, getPopProject })(ParticipateModal);