import { SET_WALLET_ADDRESS, GET_WALLET_ADDRESS, SET_BEFORE_WALLET, REDIRECT_TO_WALLET } from "../../redux/types";

const initialState = {
    accountAddress: null,
    redirectW:false
}

export default function (state = initialState, action) {
    switch (action.type) {
        case SET_WALLET_ADDRESS:
            return {
                ...state,
                accountAddress: action.payload
            }
        case SET_BEFORE_WALLET:
            return {
                ...state,
                accountAddress: null,
                redirectW:false
            }
        case REDIRECT_TO_WALLET:
            return {
                ...state,
                redirectW: action.payload
            }

        default:
            return {
                ...state
            }
    }
}