import { GET_TRENDING_NFTS, BEFORE_TRENDING_NFTS } from "../../redux/types";

const initialState = {
    nfts: [],
    getTrendingNFTs: false
}

export default function (state = initialState, action) {
    switch (action.type) {
        case GET_TRENDING_NFTS:
            return {
                ...state,
                nfts: action.payload.nfts,
                getTrendingNFTs: true
            }
        case BEFORE_TRENDING_NFTS:
            return {
                ...state,
                nfts: [],
                getTrendingNFTs: false
            }
        default:
            return {
                ...state
            }
    }
}