import { UPSERT_PARTICIPANT, BEFORE_PARTICIPANT, GET_ERRORS, GET_PARTICIPANTS, GET_PARTICIPANT, DELETE_PARTICIPANT } from "../../redux/types";

const initialState = {
    participant: null,
    participants: [],
    pagination: null,
    deleteParticipantAuth: false,
    upsertParticipantAuth: false,
    getParticipantAuth: false
}

export default function (state = initialState, action) {
    switch (action.type) {
        case UPSERT_PARTICIPANT:
            return {
                ...state,
                Participant: action.payload,
                upsertParticipantAuth: true
            }
        case DELETE_PARTICIPANT:
            return {
                ...state,
                participants: action.payload,
                deleteParticipantAuth: true
            }
        case GET_PARTICIPANTS:
            return {
                ...state,
                participants: action.payload.data,
                pagination: action.payload.pagination,
                getParticipantAuth: true
            }
        case GET_PARTICIPANT:
            return {
                ...state,
                participants: action.payload,
                getParticipantAuth: true
            }
        case BEFORE_PARTICIPANT:
            return {
                ...state,
                participant: null,
                participants: [],
                pagination: null,
                deleteParticipantAuth: false,
                upsertParticipantAuth: false,
                getParticipantAuth: false
            }
        default:
            return {
                ...state
            }
    }
}