import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { BrowserRouter as Router, Route, Switch, useHistory } from "react-router-dom";
import 'react-date-range/dist/styles.css'; // main css file
import 'react-date-range/dist/theme/default.css'; // theme css file
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import NotFound from './components/NotFound/notFound'
import React, { Suspense, useEffect } from 'react';
import routes from './routes';
import { PrivateRoute } from './privateRotue';
import { setBeforeWallet } from './components/Wallet/Wallet.action'
import ScrollToTop from './custum-hook/scrollToTop';
import { connect } from 'react-redux';
function App(props) {
  return (
    <React.Fragment>
      <Router >
        <ScrollToTop />
        <Switch>
          {
            routes.map((route, index) => {
              return (
                <PrivateRoute
                  key={index}
                  path={route.path}
                  exact={route.exact}
                  access={true}
                  access={route.access}
                  restricted={route.restricted}
                  component={props =>
                    <route.layout {...props} title={route.title} subpage={route.subpage} page={route.page}  >
                      <route.component {...props} />
                    </route.layout>
                  }
                />
              )
            })
          }
          <Route component={NotFound} />
        </Switch>
      </Router>
    </React.Fragment>
  );
}

const mapStateToProps = (state) => ({
  wallet: state.wallet,

})
export default connect(null, { setBeforeWallet })(App);