// import { SET_USER, GET_USERS, BEFORE_USER, UPDATE_PROFILE, UPDATE_PROFILE_FLAG, GET_USER } from "../../redux/types";
import { SET_USER, GET_USERS, BEFORE_USER, GET_USER, BEFORE_GET_USER, GET_FANS } from "../../redux/types";


const initialState = {
    userData: null,
    userAuth: false,
    // profileAuth:false,
    getUserAuth: false,
    getUsersAuth: false,
    getFansAuth: false,
    users: [],
    fans: []
}

export default function (state = initialState, action) {
    switch (action.type) {
        case SET_USER:
            return {
                ...state,
                userData: action.payload,
                userAuth: true
            }
        case GET_USER:
            return {
                ...state,
                userData: action.payload,
                // userAuth:true,
                getUserAuth: true
            }
        case GET_FANS:
            return {
                ...state,
                fans: action.payload,
                // userAuth:true,
                getFansAuth: true
            }
        case GET_USERS:
            return {
                ...state,
                users: action.payload.users,
                // pagination: action.payload.pagination,
                getUserAuth: true
            }
        // case UPDATE_PROFILE:
        //     return {
        //         ...state,
        //         userData: action.payload,
        //         profileAuth:true
        //     }
        // case UPDATE_PROFILE_FLAG:
        //     return {
        //         ...state,
        //         profileAuth:false
        //     }
        case BEFORE_USER:
            return {
                ...state,
                userData: null,
                userAuth: false,
                // profileAuth:false,
                getUserAuth: false,
                getUsersAuth: false,
                users: []
            }
        case BEFORE_GET_USER:
            return {
                ...state,
                getUserAuth: false,
                getUsersAuth: false,
            }
        default:
            return {
                ...state
            }
    }
}