import { GET_RECENT_NFTS, BEFORE_RECENT_NFTS } from "../../redux/types";

const initialState = {
    nfts: [],
    getRecentNFTAuth: false
}

export default function (state = initialState, action) {
    switch (action.type) {
        case GET_RECENT_NFTS:
            return {
                ...state,
                nfts: action.payload.nfts,
                getRecentNFTAuth: true
            }
        case BEFORE_RECENT_NFTS:
            return {
                ...state,
                nfts: [],
                getRecentNFTAuth: false
            }
        default:
            return {
                ...state
            }
    }
}