export const setLoggedInUser = () => dispatch => {
    const userData = ENV.getUserKeys("_id email username address profileImage description totalAmountUserInvesting nftsCount accessToken userAuth")
    // dispatch(emptyError())
    dispatch({
        type: SET_USER,
        payload: userData
    })
}

// signup action method
export const userSignUp = (payload) => dispatch => {
    // dispatch(emptyError())
    const url = `${ENV.url}auth/register`;
    fetch(url, {
        method: 'POST',
        headers: {
            'content-type': 'application/json',
        },
        body: JSON.stringify(payload)
    }).then(res => res.json()).then(data => {
        if (data.status) {
            dispatch({
                type: SET_USER,
                payload: data.data
            })
        } else {
            dispatch({
                type: GET_ERRORS,
                payload: data
            })
        }
    }).catch(errors => {
        dispatch({
            type: GET_ERRORS,
            payload: errors
        })
    })
}

//update Profile
export const updateProfile = (payload) => dispatch => {
    dispatch(emptyError())

    const url = `${ENV.url}users/update-profile`;
    fetch(url, {
        method: 'PUT',
        headers: {
            'Authorization': ENV.Authorization,
            'x-auth-token': ENV.x_auth_token
        },
        // headers: {
        //     'content-type': 'application/json',
        // },
        body: payload
    }).then(res => res.json()).then(data => {
        if (data.status) {
            toast.success(data.message);
            ENV.encryptUserData(data.data);
            console.log("dataFront: ", data.data);
            dispatch({
                type: GET_USER,
                payload: data.data
            })
        } else {
            toast.error(data.message);
            dispatch({
                type: GET_ERRORS,
                payload: data
            })
        }
    }).catch(error => {
        if (error.response && error.response.data) {
            const { data } = error.response
            if (data.message)
                toast.error(data.message)
        }
        dispatch({
            type: GET_ERRORS,
            payload: error
        })
    })
}
