import { SET_WALLET_ADDRESS, GET_WALLET_ADDRESS, SET_BEFORE_WALLET, REDIRECT_TO_WALLET, SET_WALLET_ERROR } from '../../redux/types';

export const setWalletError = (message) => {
    return {
        type: SET_WALLET_ERROR,
        payload: message
    }
}

export const setWalletAddress = (address) => dispatch => {
    dispatch({
        type: SET_WALLET_ADDRESS,
        payload: address
    })
}

export const setBeforeWallet = (address) => dispatch => {
    dispatch({
        type: SET_BEFORE_WALLET,

    })
}

export const getWalletAddress = () => dispatch => {
    dispatch({
        type: GET_WALLET_ADDRESS,
    })
}

export const redirectToWallet = () => {
    return {
        type: REDIRECT_TO_WALLET,
        payload: true
    }
}