import React, { useState, useEffect, useRef } from "react";
import { Col, Container, Row, Dropdown } from "react-bootstrap";
import { Modal, Button } from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDotCircle, faTimes, faPause } from '@fortawesome/free-solid-svg-icons';
import PayPlayBg from '../../images/Pay-play-bg.png';
import { Link, useHistory } from "react-router-dom";
import CartPopup from "../Cart/CartPopup";
import useAudio from "./audio";
import { connect } from "react-redux";
import { upsertCountSongStats } from '../songStats/songStats.action'

function Pay_or_play_popup(props) {
    // const [lgShow, setLgShow] = useState(false);

    // const hideModel = () => { setLgShow(false) }

    const [playing, toggle] = useAudio(props.nft);
    const [videoStatus, setVideoStatus] = useState(false)
    const vidRef = useRef(null);

    const [smShows, setsmShows] = useState(false);

    const hideModel1 = () => { setsmShows(false) };

    useEffect(() => {
        if (playing) {
            props.upsertCountSongStats('nfts/songCounts', JSON.stringify({ nftId: props.nft._id }))
        }

    }, [playing])
    const handlePlayVideo = () => {
        vidRef.current.play();
    }

    const handleplayButton = async () => {
        if (!props?.nft?.musicIsVideo) { toggle() }
        else {
            // if(!vidRef.paused){
            //     console.log("video",vidRef.paused)
            // }
            // console.log("video",vidRef.current,vidRef.controls)
            // vidRef.current.play();
            if (!videoStatus) {
                vidRef.current.play();
                props.upsertCountSongStats('nfts/songCounts', JSON.stringify({ nftId: props.nft._id }))
                setVideoStatus(true)
            } else {
                vidRef.current.pause();
                setVideoStatus(false)
            }
        }

    }
    return (
        <>
            <Modal
                className="house-of-cards dark-popup"
                show={props.lgShows}
                aria-labelledby="example-modal-sizes-title-lg"
            >
                <Modal.Body>
                    <Row>
                        <Col className="mobile-hide">
                            {!props?.nft?.musicIsVideo && <div className="left-side-constent text-center">
                                <img className="img-fluid" src={props?.nft?.image} />
                            </div>}
                            {props?.nft?.musicIsVideo && <div className="left-side-constent text-center">
                                {/* <img className="img-fluid" src={props?.nft?.image} /> */}
                                <video ref={vidRef} width="750" height="500" controls >
                                    <source src={props?.nft?.music} type="video/mp4" />
                                </video>
                            </div>}
                        </Col>
                        <Col>
                            <div className="pay-or-play-mobile-bg right-side-content text-center">
                                <div className="cross-btn float-end" onClick={() => props.hideModel()}>
                                    <FontAwesomeIcon icon={faTimes} />
                                </div>
                                <h1 className="title pay-title">Pay<br /> or play</h1>
                                <div dangerouslySetInnerHTML={{ __html: props?.nft?.description }}>
                                </div>
                                {/* <div>
                                    <audio src="https://gateway.ipfs.io/ipfs/QmZea8jgGgSuwog2BkSuStMaggruact7iA7Do6G6qUz5cs" controls />
                                </div> */}
                                {/* <div>
                                        <button onClick={toggle}>{playing ? "Pause":"tart to play - Free"}</button>
                                        </div>    */}
                                <div className="d-block d-sm-none left-side-constent text-center">
                                    <img className="img-fluid" src={PayPlayBg} />
                                </div>

                                <div className="buttons">

                                    <button className="btn btn-theme bg-gradient-theme mb-3 mt-4"
                                        onClick={handleplayButton
                                            //toggle
                                            // !props?.nft?.musicIsVideo &&  toggle()
                                        }
                                    >

                                        {/* {playing ? "Pause" : "start to play - Free"} */}
                                        <Link to={`/singer-details/${props?.nft?._id}`}>Start to play - Free</Link>
                                    </button>
                                    <span className="d-block">Or</span>
                                    <button className="btn btn-theme ethereum-icon d-flex bg-gray m-auto mt-3" onClick={() => setsmShows(true)}>{props?.nft?.currentPrice} ETH</button>
                                </div>
                            </div>
                        </Col>
                    </Row>
                </Modal.Body>
            </Modal>
            <div className="modal-wrapper">
                <CartPopup hideModel={hideModel1} smShows={smShows} nft={props?.nft} />
            </div>
            {/* <div className="modal-wrapper">
                <Nft_slide_modal hideModel={hideModel} lgShow={lgShow} />
            </div> */}
        </>
    )
}
export default connect(null, { upsertCountSongStats })(Pay_or_play_popup);