import React, { useState, useEffect } from "react";
import { Col, Container, Row, Dropdown, Form, Tabs, Tab } from "react-bootstrap";
import { Modal, Button, } from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDotCircle, faTimes, faPause, faChevronRight, faChevronLeft, faPlus, faMinus, faCloudUploadAlt } from '@fortawesome/free-solid-svg-icons';
import EthIcon from '../../../images/ethereum.svg'
import Upload from '../../../images/upload.png'
import Complete from '../../../images/complete-icon.png'
import FullPageLoader from '../../FullPageLoader/FullPageLoader'
import { upsertProject } from './project.action';
import { connect } from 'react-redux';
import { addDays } from 'date-fns';
import { DateRangePicker } from 'react-date-range';
import { useHistory } from "react-router";
import moment from "moment";
import $ from 'jquery';
import { FileUploader } from "react-drag-drop-files";
import { CKEditor } from '@ckeditor/ckeditor5-react';
import { toast } from 'react-toastify';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import { createProject } from '../../../utils/web3'


function CreateProjectModal(props) {
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    //create Modal
    const [mShow, setMShow] = useState(true);
    const handleMClose = () => setMShow(false);
    const handleMShow = () => setMShow(true);

    const history = useHistory();

    const [state, setState] = useState([
        {
            startDate: new Date(),
            endDate: addDays(new Date(), 7),
            key: 'selection'
        }
    ]);

    const [count, setCount] = useState(1);
    let initialProjectObject = {
        name: '',
        description: null,
        budgetGoal: '',
        startDate: new Date(),
        endDate: addDays(new Date(), 7),
        image: null,
        // email: '',
        // address: '',
        // city: '',
        // state: '',
        // zipCode: '',
    }
    const [Project, setProject] = useState(initialProjectObject)
    const [selectedImgName, setSelectedImgName] = useState('Upload Photo');
    const [fullLoader, setFullLoader] = useState(false)
    const [error, setError] = useState({})

    useEffect(() => {
        $("#createAProject").animate({ scrollTop: 0 }, "smooth");
    }, [count])

    useEffect(async () => {
        if (props.project.upsertProjectAuth) {
            let projectData = props.project.project || {};
            let message = props.project.message;
            let sartDate = new Date(projectData.startDate);
            let endDate = new Date(projectData.endDate);
            let startTimestamp = sartDate.getTime();
            let endTimestamp = endDate.getTime();
            setTimeout(() => setCount(x => x + 1), 100)
            let createProjectRes = await createProject(projectData._id, { metadata: projectData.image, name: projectData.name, budget: projectData.budgetGoal, startTime: startTimestamp, endTime: endTimestamp });
            console.log('CREATE PROJECT RESPONSE', createProjectRes)

            if (createProjectRes) {
                toast.success(message)

                let formData = new FormData()
                const { projectId, txHash } = createProjectRes
                formData.append('projectId', projectId)
                formData.append('txHash', txHash)
                props.upsertProject(`project/edit/${projectData._id}`, formData, 'PUT')
                setFullLoader(false);
            }
        }
    },
        [props.project.upsertProjectAuth]
    )
    useEffect(() => {
        if (props.error)
            setFullLoader(false)
    }, [props.error])

    const handleChange = (e) => {
        const { name, value } = e.target;
        const data = Project;
        data[name] = value;
        setProject({ ...data })

        let errorObj = error;
        delete errorObj[name]
        setError({ ...errorObj })
    }
    const handleImageChange = (e) => {
        let file = e.target.files[0]
        let fileName = e.target.files.item(0).name;
        let fileId = e.target.id
        let data = Project
        data = { ...data, [e.target.name]: file }
        setSelectedImgName(fileName)
        setProject(data)

        let errorObj = error;
        // delete errorObj.image
        delete errorObj[e.target.name]
        setError({ ...errorObj })
    }
    const handleWithDragDropChange = (file, name) => {
        let fileName = file.name;
        let data = Project
        data = { ...data, [name]: file }
        setSelectedImgName(fileName)
        setProject(data)

        let errorObj = error;
        // delete errorObj.image
        delete errorObj[name]
        setError({ ...errorObj })



    }
    const handleSubmit = async () => {

        //Append intoform Data 
        var formData = new FormData()
        for (const key in Project)
            formData.append(key, Project[key])
        await props.upsertProject('project/create', formData);
        setFullLoader(true);
    }

    const onValidation = () => {
        if (count == 0) {
            setTimeout(() => setCount(x => x + 1), 100)
        }
        else if (count == 1) {
            let file = Project.image;
            if (Project.image === null) {
                error.image = "Image is Required"
                setError({ ...error })
            }
            else {
                setTimeout(() => setCount(x => x + 1), 100)
            }

        } else if (count == 2) {
            if (Project.name == '') {
                error.name = "Name is Required"
            }
            if (!Project.description) {
                error.description = "Description is Required"
            }
            if (Project.budgetGoal == '') {
                error.budgetGoal = "Budget Goal is Required"
            }
            if (Object.keys(error).length === 0) {
                handleSubmit();
            } else {
                setError({ ...error })
            }
        }
    }

    const handleDate = (item) => {
        setState([item.selection]);
        let data = Project;
        data.startDate = item.selection.startDate;
        data.endDate = item.selection.endDate
        setProject({ ...data })
    }
    const onBackTab = () => {
        setTimeout(() => setCount(x => x - 1), 100)

    }

    const onEditorChange = (event, editor) => {
        let editorData = editor.getData();
        setProject({ ...Project, description: editorData });
        let errorObj = error;
        delete errorObj.description
        setError({ ...errorObj })
    }

    return (
        <>
            <Modal fullscreen={true}
                className="upload-modal"
                show={mShow}
                aria-labelledby="example-modal-sizes-title-lg"
            >
                <Modal.Header >
                    <Modal.Title><img src="/images/logo.svg" alt="Logo " className="img-fluid" /> </Modal.Title>
                    <div className="cross-btn float-end" onClick={
                        () => handleShow()

                        // () => props.hideModel()
                    }>
                        <FontAwesomeIcon icon={faTimes} />
                    </div>
                </Modal.Header>
                <Modal.Body id="createAProject">
                    {fullLoader ? <FullPageLoader /> : ""}

                    <Container>
                        {count == '0' ?
                            <Row>
                                <Col md={12}>
                                    <div className="content-wrapper">
                                        <h1>Get verified</h1>
                                        <p className="content">Amet minim mollit non deserunt ullamco est sit aliqua dolor do amet sint. Velit officia consequat duis enim velit mollit. Exercitation veniam consequat sunt nostrud amet.</p>
                                        <Button className="btn-white right-arrow" onClick={() => setTimeout(() => setCount(x => x + 1), 100)}>Start <FontAwesomeIcon className="ms-1" icon={faChevronRight} /></Button>
                                    </div>
                                </Col>
                            </Row>
                            : ''}
                        {/* Upload File */}
                        {count == '1' ? <div className="upload-file">
                            <Row>
                                <Col md={12}>
                                    <div className="content-wrapper">
                                        <div className="icon">
                                            <img src={Upload} alt="" className="img-fluid" />
                                        </div>

                                        <h1>upload project log</h1>
                                        <p className="content">Amet minim mollit non deserunt ullamco est sit aliqua dolor do amet sint. Velit officia consequat duis enim velit mollit. Exercitation veniam consequat sunt nostrud amet.</p>
                                        {/* <div className="select-file-btn"><input type="file" name="image" accept="image/*" onChange={(e) => handleImageChange(e)} /><Button className="btn-outline-white right-arrow"><FontAwesomeIcon className="me-2" icon={faCloudUploadAlt} />{selectedImgName} </Button></div> */}
                                        <div className="file-drop-zone">
                                            <FileUploader handleChange={(e) => handleWithDragDropChange(e, "image")} accept="image/*" name="file" types={["png", "jpg", "jpeg", "gif"]} />
                                        </div>
                                        {error?.image && <div className="text-red"> This field is required</div>}

                                        {/* <Button className="btn-white right-arrow"><FontAwesomeIcon className="me-1" icon={faChevronRight} /> Upload complete </Button> */}
                                        <br />
                                        <p>Or</p>
                                        <br />
                                        <p>drag the file directly in<br /> your browser</p>
                                        <p className="file-type-info">Compatible format : .png, .jpg, .svg, .gif</p>
                                    </div>
                                </Col>
                            </Row>
                        </div> : ''}


                        {/* Song Information */}
                        {count == '2' ? <div className="song-info">
                            <Row>
                                <Col md={12}>
                                    <div className="content-wrapper">
                                        <h1>About project</h1>
                                        <p className="content">Amet minim mollit non deserunt ullamco est sit aliqua dolor do amet sint. Velit officia consequat duis enim velit mollit. Exercitation veniam consequat sunt nostrud amet.</p>
                                        <Form>
                                            <Form.Group className="mb-3">
                                                <Form.Label>Name</Form.Label>
                                                <Form.Control type="text" name="name" onChange={handleChange} tabIndex="1" />
                                                {error?.name && <div className="text-red text-start"> This field is required*</div>}
                                            </Form.Group>
                                            <Form.Group className="mb-3">
                                                <Form.Label>Description</Form.Label>
                                                <CKEditor
                                                    editor={ClassicEditor}
                                                    data={Project.description}
                                                    content={Project.description}
                                                    onChange={(event, editor) => onEditorChange(event, editor)}
                                                />
                                                {error?.description && <div className="text-red text-start"> This field is required*</div>}
                                            </Form.Group>
                                            <Form.Group className="mb-3">
                                                <Form.Label>Budget Goal</Form.Label>
                                                <Form.Control type="text" name="budgetGoal" onChange={handleChange} tabIndex="3" />
                                                {error?.budgetGoal && <div className="text-red text-start"> This field is required*</div>}
                                            </Form.Group>

                                            <Form.Group className="mb-3 date-picker d-flex flex-column">
                                                <Form.Label>Start Date &amp; End Date</Form.Label>


                                                <DateRangePicker
                                                    onChange={handleDate
                                                        //    item => setState([item.selection])
                                                    }
                                                    showSelectionPreview={false}
                                                    moveRangeOnFirstSelection={true}
                                                    months={2}
                                                    ranges={state}
                                                    direction="horizontal"
                                                />
                                            </Form.Group>
                                        </Form>
                                    </div>
                                </Col>
                            </Row>
                        </div> : ''}

                        {count == '3' ? <div className="quantity-prices">
                            <Row>
                                <Col md={12}>
                                    <div className="content-wrapper">
                                        <div className="icon">
                                            <img src={Complete} alt="" className="img-fluid" />
                                        </div>
                                        <h1>Your informations have been sent</h1>
                                        <p className="content">Amet minim mollit non deserunt ullamco est sit aliqua dolor do amet sint. Velit officia consequat duis enim velit mollit. Exercitation veniam consequat sunt nostrud amet.</p>

                                        <a href="/artist/dashboard" className="btn-white right-arrow"> Got to the dashboard <FontAwesomeIcon className="ms-1" icon={faChevronRight} /></a>

                                    </div>
                                </Col>
                            </Row>
                        </div> : ""}


                    </Container>
                </Modal.Body>
                {count > '0' && count < '3' ? < Modal.Footer >
                    <div className=" footer-btns">
                        <Button className="btn-gray right-arrow bg-gradient-circle" onClick={onBackTab}
                        // onValidation()
                        // () => setTimeout(() => setCount(x => x - 1), 100)

                        > <FontAwesomeIcon icon={faChevronLeft} />  </Button>
                        <Button className="btn-white right-arrow" tabIndex="6" onClick={() => {
                            onValidation();

                            // setTimeout(() => setCount(x => x + 1), 100)
                        }} >Confirm <FontAwesomeIcon className="ms-1" icon={faChevronRight} />  </Button>
                    </div>
                </Modal.Footer > : ''}

            </Modal>
            <Modal
                show={show}
                onHide={handleClose}
                backdrop="static"
                keyboard={false}
                className="delete-data-modal"
            >
                <Modal.Header closeButton>
                    <Modal.Title className="purple">Delete</Modal.Title>
                </Modal.Header>
                <Modal.Body className="my-5 text-center">
                    Your data will be Lost.If you Close this.
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="danger" onClick={handleClose}>
                        Close
                    </Button>
                    <Button variant="primary" className="bg-gradient-theme" onClick={() => {
                        //close are you sure model
                        handleClose()
                        //close enterie artist modal
                        // props.hideModel()
                        handleMClose()
                        setProject({ ...initialProjectObject })
                        setSelectedImgName('Upload Photo')
                        setError({})
                        setCount(0)
                        history.push("/artist/dashboard")
                    }

                    }>Understood</Button>
                </Modal.Footer>
            </Modal>
        </>
    )
}
const mapStateToProps = (state) => ({
    project: state.projects,
    error: state.error
})
export default connect(mapStateToProps, { upsertProject })(CreateProjectModal);