import React from "react";
import { Col } from "react-bootstrap";
import MusicImg from '../../images/music-img.png'
import './MusicBox.css'

function MusicBox(props) {
    return (
        <Col md={4} xs={6}>
            <div className="music-box mt-3">
                <div className="music-box-pic">
                    <img src={props?.nft?.image ? props?.nft?.image : MusicImg} className="img-fluid music-box-in-pic" />
                </div>
                <div className="music-content ms-2">
                    <h6>{props?.nft?.name}</h6>
                    <span>{props?.nft?.creator?.username}</span>
                </div>
            </div>
        </Col>
    )
}

export default MusicBox;