import React, { useEffect, useState } from "react";
import { Col, Container, Row, Tab, Tabs } from "react-bootstrap";
import Header from "../Header/Header";
import SongsList from "../SongsList/SongsList";
import ProjectList from "../SongsList/ProjectsList";
import { connect } from 'react-redux'
import { getNFTs, beforeNFT } from '../Upload_create_modal/nft.action'
import { getProjects, beforeProject } from '../Create_project_modal/project.action'
import FullPageLoader from "../../FullPageLoader/FullPageLoader";
import { ENV } from '../../../config/config'
import { useHistory } from "react-router-dom";

function SongsProjects(props) {
  const [loader, setLoader] = useState(true);
  const [nfts, setSongs] = useState([])
  const [projects, setProjects] = useState([])
  const history = useHistory();

  useEffect(() => {
    const { _id } = ENV.getUserKeys('_id')

    // list of songs
    props.beforeNFT()
    props.getNFTs(`ownerId=${_id}&allListing=true`)

    // list of projects
    props.beforeProject()
    props.getProjects(`ownerId=${_id}`)
  }, [])

  useEffect(async () => {
    // when user is on this screen and adds NFT successfully
    if (props.nfts.upsertNFTAuth) {
      setLoader(true)
      props.getNFTs(`ownerId=${_id}&allListing=true`)
    }
  }, [])

  useEffect(() => {
    setLoader(false)
    if (props.nfts.getNFTAuth) {
      const { nfts } = props.nfts
      setSongs(nfts)
      props.beforeNFT()
      console.log("artists")
      history.push("/artist/songs-projects")
    }
  }, [props.nfts.getNFTAuth])

  useEffect(() => {
    if (props.projects.getProjectsAuth) {
      const { projects } = props.projects
      setProjects(projects)
      props.beforeProject()
    }
  }, [props.projects.getProjectsAuth])

  return (
    <>
      {
        loader ?
          <FullPageLoader /> :
          <>
            <div className="artist-dashboard">
              <Header />
              <div className="songs-projects main-padding-top">
                <Container>
                  <Row>
                    <Col xs={12}>
                      <div className="d-flex gap-3 justify-content-center">
                        <span className="sp-head green-yellow">Songs</span>
                        <span className="and-font">&amp;</span>
                        <span className="sp-head green">Projects</span>
                      </div>
                    </Col>
                  </Row>
                  <Row>
                    <Col xs={12}>
                      <Tabs defaultActiveKey="songs" id="uncontrolled-tab-example" className="margin-bottom">
                        <Tab eventKey="songs" title="Songs">
                          {nfts.length > 0 ? nfts.map((nft, index) => {
                            return <SongsList key={index} nft={nft} />
                          }) :
                            <div className="home-slider-container">
                              <span className="text-danger">No data available</span>
                            </div>
                          }

                        </Tab>
                        <Tab eventKey="projects" title="Projects">
                          {projects.length > 0 ? projects?.map((project, index) => {
                            return <ProjectList key={index} project={project} />

                          }) :
                            <div className="home-slider-container">
                              <span className="text-danger">No data available</span>
                            </div>
                          }
                        </Tab>
                      </Tabs>
                    </Col>
                  </Row>
                </Container>
              </div>
            </div>
          </>
      }
    </>
  )
}

const mapStateToProps = (state) => ({
  nfts: state.nfts,
  projects: state.projects
})
export default connect(mapStateToProps, { beforeNFT, beforeProject, getNFTs, getProjects })(SongsProjects);