import React, { useEffect } from 'react';
import { Redirect, Route } from "react-router-dom";
import { ENV } from './config/config'
export const PrivateRoute = ({ component: Component, restricted, access, ...rest }) => {
    const isLogin = () => {
        let isAuth = false;
        try {
            let user = ENV.getUserKeys('accessToken _id status')
            if (user.accessToken) {
                isAuth = true
            }
        } catch (e) {
            console.log("Error", e)
        }
        return isAuth
    }
    return <Route
        {...rest}
        render={props =>
            access === true ?
                (<Component {...props} />)
                :
                isLogin() ? (<Component {...props} />) : <Redirect to="/login" />
        } />
};
