import React, { useEffect, useState } from "react";
import { Link, useHistory } from "react-router-dom";
import { Col, Container, Row, Navbar, Dropdown } from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlayCircle, faSearch, faThLarge, faChartLine } from '@fortawesome/free-solid-svg-icons';
// import './Header.css';
import DroppdownPopup from '../../DropdownPopup/DropdownPopup.js'
import UploadModal from '../Upload_create_modal/Upload_create_modal.js'
import { getSingerDetails } from "../../SingerDetails/singer.action.js";
import imagePlaceholder from "../../../images/default-avatar.png";
import { ENV } from "../../../config/config"

function Header() {
    const [lgShows, setLgShows] = useState(false);
    const hideModels = () => { setLgShows(false) }
    const [isopen, setisopen] = useState(false);
    const isopenhide = () => { setisopen(false) }
    const [user, setUser] = useState({})
    const history = useHistory();
    const pathname = history.location.pathname;

    useEffect(() => {
        let user = ENV.getUserKeys('accessToken _id status profileImage');
        setUser(user);
    }, [])

    const formattedAddress = (address) => {
        return `${address.slice(0, 5)}...${address.slice(-5)}`
    }

    return (
        <>
            <header id="header">
                <Container>
                    <Navbar collapseOnSelect expand="xl" className="pos-stat dashboard-header navbar-dark">

                        <Navbar.Brand className="logo-img" href="/"><img className="img-fluid" src="/images/logo.svg" /></Navbar.Brand>
                        <Navbar.Toggle aria-controls="basic-navbar-nav" />
                        <Navbar.Collapse id="basic-navbar-nav">
                            <ul className="navbar-nav m-auto">
                                <span className="material-icons-outlined"></span>
                                <li className={`nav-item ${pathname === '/artist/dashboard' ? 'active' : ''}`}><Link className="nav-link" to="/artist/dashboard"><i className="nav-icon"><FontAwesomeIcon icon={faThLarge} /></i>Dashboard </Link></li>
                                <li className={`nav-item ${pathname === '/artist/business' ? 'active' : ''}`}><Link className="nav-link" to="/artist/business"><i className="nav-icon"><FontAwesomeIcon icon={faChartLine} /></i>Business</Link></li>
                                <li className={`nav-item ${pathname === '/artist/songs-projects' ? 'active' : ''}`}><Link className="nav-link" to="/artist/songs-projects"><i className="nav-icon"><FontAwesomeIcon icon={faPlayCircle} /></i>Songs &amp; Projects</Link></li>
                                <button type="button" className="d-flex d-xl-none btn btn-theme upload-icon d-flex bg-gradient-green justify-content-center" onClick={() => setLgShows(true)}><span>Upload / Create</span></button>

                            </ul>
                        </Navbar.Collapse>

                        <div className="user-icon-1 d-flex justify-content-end align-items-center gap-4 " >
                            <button type="button" className="d-none d-xl-flex btn btn-theme upload-icon d-flex bg-gradient-green" onClick={() => setLgShows(true)}><span>Upload / Create</span></button>
                            <div className="header-2-img float-end">
                                <img className="img-fluid header-2-in-img" onClick={() => setisopen(true)} src={user?.profileImage ? user?.profileImage : imagePlaceholder} />
                            </div>
                        </div>
                        <Dropdown className="ms-4">
                            <Dropdown.Toggle id="dropdown-basic" className="btn-white-1">
                                <span className="header-wallet-address text-black"> {localStorage.getItem('wallet_address') && formattedAddress(localStorage.getItem('wallet_address'))}</span>

                            </Dropdown.Toggle>
                            <Dropdown.Menu>
                                <Dropdown.Item as={Link} to="/profile">Profile</Dropdown.Item>
                                {/* <Dropdown.Item as={Link} to="/authors-list">Artists</Dropdown.Item> */}
                                <Dropdown.Item as={Link} to="/my-albums">My Albums</Dropdown.Item>
                                <Dropdown.Item as={Link} to="/library">Library</Dropdown.Item>
                                <Dropdown.Item as={Link} to="/logout">Logout</Dropdown.Item>

                            </Dropdown.Menu>
                        </Dropdown>


                    </Navbar>
                </Container>
                <DroppdownPopup isopenhide={isopenhide} isopen={isopen} />

            </header>
            <div className="modal-wrapper">
                <UploadModal hideModel={hideModels} lgShows={lgShows} />
            </div>
        </>
    )
}
export default Header;