import React, { useEffect, useState } from "react";
import { Col, Container, Row } from "react-bootstrap";
import './SingerDetails.css'
import Singer from '../../images/Singer.svg'
import Header from "../Header/Header";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowLeft, faUsers, faCheckCircle } from '@fortawesome/free-solid-svg-icons';
import LibraryTabs from "../LibraryTabs/LibraryTabs";
import { Link, useHistory, useParams } from "react-router-dom";
import { connect } from "react-redux";
import { getSingerDetails, addVisitor } from './singer.action';
import { doFollow, checkFollowerShip, unFollow } from '../Followers/follower.action'
import FullPageLoader from "../FullPageLoader/FullPageLoader";

function SingerDetails(props) {
    const [follow, setFollow] = useState({ followedTo: '' })
    const [fullLoader, setFullLoader] = useState(true)
    const [followBtnText, setFollowBtnText] = useState('')
    const [incrementTotalFollowers, setIntrementTotalFollowers] = useState(0);
    const { id } = useParams();

    useEffect(() => {
        props.checkFollowerShip(props?.match?.params?.id)
        props.getSingerDetails(props?.match?.params?.id);
        setFollow({ ...follow, followedTo: props?.match?.params?.id })
        addVisitor(id);
    }, [])

    useEffect(() => {
        if (props.singers.getSingerDetailAuth) {
            setFullLoader(false)
        }
    }, [props.singers.getSingerDetailAuth])

    useEffect(() => {
        if (props.follow.follow) {
            let text = props.follow.follow.alreadyFollow ? "Following" : "Follow"

            setFollowBtnText(text);
        }
    }, [props.follow.checkFollowerShipAuth])
    // useEffect(() => {
    //     if (props.follow.doSignerFollowAuth) {
    //         setFollowBtnText('Following')
    //     }
    //     if (props.follow.doSignerUnFollowAuth) {
    //         setFollowBtnText('Follow')
    //     }
    //     setFullLoader(false);

    // }, [props.follow.doSignerFollowAuth, props.follow.doSignerUnFollowAuth])


    const handleFollow = async () => {
        //Append intoform Data 
        var formData = new FormData()
        for (const key in follow)
            formData.append(key, follow[key])

        if (!props.follow.follow.alreadyFollow) {
            await props.doFollow('users/follow', formData);
            await props.checkFollowerShip(props?.match?.params?.id)
            setIntrementTotalFollowers(1)
            setFollowBtnText('Following')
        }
        else {
            await props.unFollow('users/unfollow', formData)
            setIntrementTotalFollowers(0)
            setFollowBtnText('Follow')
        }
        setFullLoader(true);
    }

    return (
        <>
            {fullLoader && <FullPageLoader />}
            <div className="main-wrapper">
                {/* <Header /> */}
                <Container className="artist-details">
                    <Row>
                        <Col>
                            <div className="p-relative d-flex justify-content-center align-items-center">
                                <div className="arrow-button">
                                    <Link to="/authors-list" className=" back">
                                        <FontAwesomeIcon icon={faArrowLeft} />
                                    </Link>
                                </div>
                                <div className="singer-detail-head">
                                    <span className="title">Artist Details</span>
                                </div>
                            </div>
                        </Col>
                    </Row>
                    <Row className="mt-5">
                        <Col md={6}>
                            <div className="singerFeatureImg">
                                <img className="img-fluid singer-img-in" src={
                                    props?.singers?.singer?.profileImage ? props?.singers?.singer?.profileImage :
                                        Singer} />
                            </div>
                        </Col>
                        <Col md={6}>
                            <div className="content singer-details-content">
                                <div className="signer-details-mobile-bg">
                                    {/* <div className="d-flex d-md-none singerFeatureImg">
                                    <img className="img-fluid" src={Singer} />
                                </div> */}
                                    {/* <Link to="/" className="d-flex d-md-none back mobile">
                                    <FontAwesomeIcon icon={faArrowLeft} />
                                </Link> */}
                                    <div className="artist-details-1">
                                        {/* <Link to="/" className="d-none d-md-flex back">
                                        <FontAwesomeIcon icon={faArrowLeft} />
                                    </Link> */}
                                        <div>
                                            <h1 className="title">{props?.singers?.singer?.username}</h1>
                                        </div>
                                        {/* <div>
                                            <span className="sub-catregory mt-3">Electro</span>
                                        </div> */}

                                        {props?.singers?.singer && <div className="text-white">
                                            <div className="singer-email">
                                                {/* <span className="email-label">Email Address:</span> */}
                                                <span className="ans-label">{props?.singers?.singer?.email}</span>
                                            </div>
                                            <div className="singer-w-address mt-3">
                                                {/* <span className="email-label">Wallet Address:</span> */}
                                                <span className="ans-label">{props?.singers?.singer?.address}</span>
                                            </div>
                                            {props?.singers?.singer?.identityVerification == 0 && <div className="singer-w-address mt-3">
                                                {/* <span className="email-label">Wallet Address:</span> */}
                                                <span className="ans-label"><FontAwesomeIcon icon={faCheckCircle} className="tick-icon text-success"></FontAwesomeIcon></span>
                                            </div>}
                                            {<div className="singer-w-address mt-3">
                                                <span className="email-label-1"><FontAwesomeIcon icon={faUsers} /></span>
                                                <span className="ms-3 ans-label-follow">{Number(props?.singers?.singer?.totalNumberOfFollowers ? props?.singers?.singer?.totalNumberOfFollowers : 0) + Number(incrementTotalFollowers)} Followers</span>
                                            </div>}
                                        </div>}
                                        <div className="d-flex mt-3 inline-buttons">
                                            {/* <button className="btn btn-theme ethereum-icon d-flex bg-gradient-theme" type="button">
                                                Invest
                                            </button> */}
                                            {props?.match?.params?.id !== props?.singers?.singer?._id && props.follow.follow && <button className="btn btn-theme bg-white" type="button"
                                                disabled={props?.match?.params?.id == props?.singers?.singer?._id}
                                                onClick={handleFollow}>
                                                {/* {props.follow.follow.alreadyFollow ? "Following" : "Follow"} */}
                                                {followBtnText}
                                            </button>}
                                        </div>


                                    </div>
                                    {/* <div className="tabs mt-4">
                                        <LibraryTabs />
                                    </div> */}
                                </div>
                            </div>
                        </Col>
                    </Row>
                    <Row className="mt-5">
                        <Col>
                            <div className="tabs mt-5">
                                <LibraryTabs userId={props?.match?.params?.id} description={props?.singers?.singer?.description} />
                            </div>
                        </Col>
                    </Row>
                </Container>

            </div>

        </>
    )
}


const mapStateToProps = (state) => ({
    singers: state.singers,
    follow: state.follow
})


export default connect(mapStateToProps, { getSingerDetails, doFollow, checkFollowerShip, unFollow })(SingerDetails);