import { emptyError } from '../../redux/shared/error/error.action';
import { ENV } from '../../config/config';
import { BEFORE_COUNT_SONG_STATS, CREATE_COUNT_SONG_STATS, GET_ERRORS } from "../../redux/types";

export const beforeSongSTATS = () => {
    return {
        type: BEFORE_COUNT_SONG_STATS
    }
}

export const upsertCountSongStats = (apiURL, body, method = 'POST') => dispatch => {
    dispatch(emptyError());
    const url = `${ENV.url}${apiURL}`;

    fetch(url, {
        method,
        headers: {
            'Authorization': ENV.Authorization,
            'x-auth-token': ENV.x_auth_token,
            'x-access-token': ENV.getUserKeys('accessToken') && ENV.getUserKeys('accessToken').accessToken ? ENV.getUserKeys('accessToken').accessToken : '',
            'content-type': 'application/json'
        },
        body
    }).then(res => res.json()).then(data => {
        if (data.success) {
            // toast.success(data.message)
            dispatch({
                type: CREATE_COUNT_SONG_STATS,
                payload: data
            })
        } else {
            // toast.error(data.message)
            dispatch({
                type: GET_ERRORS,
                payload: data
            })
        }
    }).catch(error => {
        if (error.response && error.response.data) {
            const { data } = error.response
            // if (data.message)
            //     toast.error(data.message)
        }
        dispatch({
            type: GET_ERRORS,
            payload: error
        })
    })
};
