import React, { useState, useEffect } from "react";
import { Modal } from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import './salePriceSetter.css';

const SalePriceSettter = ({ priceSetterShow, hideModel, update, nftId }) => {

    const [price, setPrice] = useState();
    const [error, setError] = useState();

    const handleOnSubmit = (e) => {
        e.preventDefault();
        if (!price || price === "0")
            setError("price should be greater than 0");
        else {
            var formData = new FormData()
            formData.append('currentPrice', price)
            formData.append('status', 1)
            update(`nfts/edit/${nftId}`, formData, 'PUT');
        }
    }

    return (
        <>
            <Modal
                className="sale-price-setter"
                show={priceSetterShow}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Body>
                    <form className="sale-price-setter-content" onSubmit={handleOnSubmit}>
                        <div className="cross-btn float-end" onClick={() => {
                            hideModel();
                        }}>
                            <FontAwesomeIcon icon={faTimes} />
                        </div>
                        <h2 className="sub-heading mb-4">Sale Price Configuration</h2>
                        <div className="price-details py-3">
                            <div className="row">
                                <div className="col d-flex flex-column align-items-center">
                                    <label htmlFor="salePriceSetter">Price</label>
                                    <input id="salePriceSetter" className="form-control" type="number" min="0" onChange={(e) => {
                                        setError();
                                        setPrice(e.target.value)
                                    }} />
                                    {error && <div style={{ color: "red" }}>{error}</div>}
                                </div>
                            </div>
                        </div>
                        <div className="d-flex justify-content-center">
                            <button className="btn btn-info" type="submit">Set Sale Price</button>
                        </div>
                    </form>
                </Modal.Body>
            </Modal>
        </>
    )
}

export default SalePriceSettter;