import { emptyError } from '../../redux/shared/error/error.action';
import { ENV } from './../../config/config';
import { UPSERT_PARTICIPANT, BEFORE_PARTICIPANT, GET_ERRORS, GET_PARTICIPANTS, GET_PARTICIPANT, DELETE_PARTICIPANT } from "../../redux/types";
import { toast } from 'react-toastify';
export const beforeParticipant = () => {
    return {
        type: BEFORE_PARTICIPANT
    }
}


export const upsertParticipant = (apiURL, body, method = 'POST') => dispatch => {
    dispatch(emptyError());
    const url = `${ENV.url}${apiURL}`;

    fetch(url, {
        method,
        headers: {
            'Authorization': ENV.Authorization,
            'x-auth-token': ENV.x_auth_token,
            'x-access-token': ENV.getUserKeys('accessToken') && ENV.getUserKeys('accessToken').accessToken ? ENV.getUserKeys('accessToken').accessToken : ''
        },
        body
    }).then(res => res.json()).then(data => {
        if (data.success) {
            toast.success(data.message)
            dispatch({
                type: UPSERT_PARTICIPANT,
                payload: data
            })
        } else {
            toast.error(data.message)
            dispatch({
                type: GET_ERRORS,
                payload: data
            })
        }
    }).catch(error => {
        if (error.response && error.response.data) {
            const { data } = error.response
            if (data.message)
                toast.error(data.message)
        }
        dispatch({
            type: GET_ERRORS,
            payload: error
        })
    })
};

export const getParticipant = (id, qs = '') => dispatch => {
    dispatch(emptyError());
    let url = `${ENV.url}nfts/get/${id}`;
    if (qs)
        url += `?${qs}`

    fetch(url, {
        method: 'GET',
        headers: {
            'content-type': 'application/json',
            'Authorization': ENV.Authorization,
            'x-auth-token': ENV.x_auth_token
        }
    }).then(res => res.json()).then(data => {
        if (data.success) {
            // if (!qs)
            //     toast.success(data.message)
            dispatch({
                type: GET_PARTICIPANT,
                payload: data.nft
            })
        } else {
            // if (!qs)
            //     toast.error(data.message)
            dispatch({
                type: GET_ERRORS,
                payload: data
            })
        }
    }).catch(error => {
        if (error.response && error.response.data) {
            const { data } = error.response
            // if (data.message)
            //     toast.error(data.message)
        }
        dispatch({
            type: GET_ERRORS,
            payload: error
        })
    })
};

