import React, { useState, useEffect } from "react";
import { Col, Container, Row, Dropdown, Section } from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlayCircle } from '@fortawesome/free-solid-svg-icons';
import Slider from "react-slick";
import NftSlide from "../Nft_slide/Nft_slide";
import './Nft.css';
import HouseOfCard from "../HouseOfCard/HouseOfCard";
import { connect } from "react-redux";
import { getRecentNfts, beforeRecentNFT } from "./recentNFTs.action";
import { useHistory } from 'react-router-dom'
import FullPageLoader from "../FullPageLoader/FullPageLoader";

const settings = {
  dots: false,
  infinite: false,
  speed: 500,
  slidesToShow: 3,
  slidesToScroll: 1,
  arrows: false,
  responsive: [
    {
      breakpoint: 1399,
      settings: {
        slidesToShow: 3,
        slidesToScroll: 1
      }
    },
    {
      breakpoint: 1199,
      settings: {
        slidesToShow: 2,
        slidesToScroll: 1
      }
    },
    {
      breakpoint: 767,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1
      }
    }
  ]
};
function Nft(props) {
  const history = useHistory()
  const [nfts, setNfts] = useState();
  const [loader, setLoader] = useState(true);

  useEffect(() => {
    props.getRecentNfts()
  }, [])

  useEffect(() => {
    if (props.recentNFTs.getRecentNFTAuth) {
      setNfts(props.recentNFTs.nfts);
      props.beforeRecentNFT();
    }
  }, [props.recentNFTs.getRecentNFTAuth])

  useEffect(() => {
    if (nfts)
      setLoader(false);
  }, [nfts])

  useEffect(() => {
    if (props.error)
      setLoader(false)
  }, [props.error])

  return (
    <>
      {loader ? <FullPageLoader /> :
        <div className="tops-section">
          <Container>
            <Row className="margin-bottom">
              <Col md={6} xs={8}>
                <h2 className="sub-heading">Most Traded NFTs</h2>
              </Col>
              {nfts?.length && <Col md={6} xs={4}>
                <div className="float-end">
                  <button type="button" className="btn btn-theme bg-gradient-theme" onClick={() => { history.push("/songs-and-projects") }}>Show all</button>
                </div>
              </Col>}
            </Row>
            <Row className="most-nft-slider-section">
              <Col>
                {!nfts?.length && <div className="home-slider-container">
                  <span className="text-danger">No data available</span>
                </div>}
                {nfts && <div className="home-page-slider">
                  <Slider className="home-slider mt-2" {...settings}>
                    {nfts.map((nft, index) => {
                      return (
                        <div key={index} className="slide">
                          <NftSlide nft={nft} />
                        </div>
                      )
                    })}
                  </Slider>
                </div>}
              </Col>
            </Row>
          </Container>
          <HouseOfCard />
        </div>}
    </>


  )
}

const mapStateToProps = (state) => ({
  recentNFTs: state.recentNFTs,
  error: state.error
})

export default connect(mapStateToProps, { getRecentNfts, beforeRecentNFT })(Nft);