import React, { useEffect } from "react";
import { Tabs, Tab, Row, Col, Button } from "react-bootstrap";
import TrendingSlide from "../Trending_slide/Trending_slide";
import { connect } from 'react-redux';
import { getNFTs } from '../ArtistInterface/Upload_create_modal/nft.action';
import { getProjects } from '../ArtistInterface/Create_project_modal/project.action';
import { getAlbums } from '../Create-Album/album.action';
import Project_Tab from "./projectTab";
import Album_Tab from "./AlbumTab";
import { ENV } from "../../config/config";
import SingerDetails from "../SingerDetails/SingerDetails";


function LibraryTabs(props) {
    const { userId } = props;
    useEffect(() => {
        console.log("singer", userId)
        const qs1 = ENV.objectToQueryString({ creatorId: userId });
        const qs2 = ENV.objectToQueryString({ userId });
        props.getNFTs(qs1);
        props.getProjects(qs1);
        props.getAlbums(qs2);
        // props.getNFTs();
        // props.getProjects();
        // props.getAlbums();
    }, [])
    const handleLoadMoreProjects = () => {
        let qs = ENV.objectToQueryString({ creatorId: userId, page: props?.projects?.pagination?.page + 1, limit: 9 })
        // let qs = `page=${props?.projects?.pagination?.page + 1}&limit${9}`;
        props.getProjects()
    }

    const handleLoadMoreNFTs = () => {
        let qs = ENV.objectToQueryString({ creatorId: userId, page: props?.nfts?.pagination?.page + 1, limit: 9 });
        // let qs = `page=${props?.nfts?.pagination?.page + 1}&limit${9}`;
        props.getNFTs()
    }
    const handleLoadMoreAlbums = () => {
        let qs = ENV.objectToQueryString({ userId, page: props?.albums?.pagination?.page + 1, limit: 9 });
        // let qs = `page=${props?.albums?.pagination?.page + 1}&limit${9}`;
        props.getAlbums()
    }

    return (
        <div className="library-tabs-section">
            <Tabs defaultActiveKey="songs" id="uncontrolled-tab-example" className="margin-bottom">
                <Tab eventKey="songs" title="Songs">
                    {props?.nftsData.length > 0 ? <Row>
                        <Col>
                            {props?.nftsData?.map((nft, index) => {
                                return (<Row key={index}>
                                    <Col md={12}>
                                        <TrendingSlide nft={nft} />
                                        <hr />
                                    </Col>
                                </Row>)


                            })}
                            {props?.nfts?.pagination?.page < props?.nfts?.pagination?.pages &&
                                <Row>
                                    <Col md={12}>
                                        <div className="load-button text-center">
                                            <Button className="btn load-btn bg-gradient-theme mt-5" onClick={handleLoadMoreNFTs}>Load More</Button>
                                        </div>
                                    </Col>
                                </Row>}
                        </Col>
                    </Row>
                        :
                        <Row>
                            <Col>
                                <div className="home-slider-container"><span className="text-danger">No Item Found</span></div>
                            </Col>
                        </Row>}
                </Tab>
                <Tab eventKey="Projects" title="Projects">
                    {props?.projects?.projects.length > 0 ? <Row>
                        <Col>
                            {props?.projects?.projects?.map((nft, index) => {
                                return <Project_Tab nft={nft} key={nft._id} />
                            })}

                            {props?.projects?.pagination?.page < props?.projects?.pagination?.pages &&
                                <Row>
                                    <Col md={12}>
                                        <div className="load-button text-center">
                                            <Button className="btn load-btn bg-gradient-theme mt-5" onClick={handleLoadMoreProjects}>Load More</Button>
                                        </div>
                                    </Col>
                                </Row>}
                        </Col>
                    </Row> :
                        <Row>
                            <Col>
                                <div className="home-slider-container"><span className="text-danger">No Item Found</span></div>
                            </Col>
                        </Row>}
                </Tab>
                <Tab eventKey="Albums" title="Albums">
                    {props?.albums?.albums.length > 0 ? <Row>
                        <Col>
                            {props?.albums?.albums?.map((nft, index) => {
                                return <Album_Tab key={index} nft={nft} />


                            })}

                            {props?.albums?.pagination?.page < props?.albums?.pagination?.pages &&
                                <Row>
                                    <Col md={12}>
                                        <div className="load-button text-center">
                                            <Button className="btn load-btn bg-gradient-theme mt-5" onClick={handleLoadMoreAlbums}>Load More</Button>
                                        </div>
                                    </Col>
                                </Row>}
                        </Col>
                    </Row> :
                        <Row>
                            <Col>
                                <div className="home-slider-container"><span className="text-danger">No Item Found</span></div>
                            </Col>
                        </Row>}
                </Tab>
                {props.description && <Tab eventKey="description" title="Description">
                    {props?.description ? <Row>
                        <Col>
                            {props?.description &&
                                <Row>
                                    <Col md={12}>
                                        <div className="singer-discriptiona-tab">
                                            <p>{props?.description}</p>
                                        </div>
                                    </Col>
                                </Row>}
                        </Col>
                    </Row>
                        :
                        <Row>
                            <Col>
                                <div className="home-slider-container"><span className="text-danger">No Item Found</span></div>
                            </Col>
                        </Row>}
                </Tab>}
            </Tabs>
        </div>

    )
}

const mapStateToProps = (state) => ({
    nftsData: state?.nfts?.nfts,
    nfts: state?.nfts,
    projects: state?.projects,
    albums: state?.albums
})
export default connect(mapStateToProps, { getNFTs, getProjects, getAlbums })(LibraryTabs);