import { emptyError } from '../../redux/shared/error/error.action';
import { ENV } from './../../config/config';
import { GET_ERRORS,BEFORE_FOLLOW, DO_SINGER_FOLLOW, CHECK_FOLLOWERSHIP,DO_SINGER_UNFOLLOW } from "../../redux/types";
import { toast } from 'react-toastify'
export const beforeFollow = () => {
    return {
        type: BEFORE_FOLLOW
    }
}

export const doFollow = (apiURL, body, method = 'POST') => dispatch => {
    dispatch(emptyError());
    const url = `${ENV.url}${apiURL}`;

    fetch(url, {
        method,
        headers: {
            'Authorization': ENV.Authorization,
            'x-auth-token': ENV.x_auth_token,
            'x-access-token': ENV.getUserKeys('accessToken') && ENV.getUserKeys('accessToken').accessToken ? ENV.getUserKeys('accessToken').accessToken : ''

        },
        body
    }).then(res => res.json()).then(data => {
        if (data.success) {
            toast.success(data.message)
            dispatch({
                type: DO_SINGER_FOLLOW,
                payload: data
            })
        } else {
            toast.error(data.message)
            dispatch({
                type: GET_ERRORS,
                payload: data
            })
        }
    }).catch(error => {
        if (error.response && error.response.data) {
            const { data } = error.response
            if (data.message)
                toast.error(data.message)
        }
        dispatch({
            type: GET_ERRORS,
            payload: error
        })
    })
};
