import { emptyError } from '../../../redux/shared/error/error.action';
import { ENV } from '../../../config/config';
import { BEFORE_HISTORY, GET_HISTORY, GET_ERRORS } from "../../../redux/types";

export const beforeHistory = () => {
    return {
        type: BEFORE_HISTORY
    }
}

export const getHistory = (type = null, qs) => dispatch => {
    dispatch(emptyError());
    let url = `${ENV.url}activity/list/${type}`;
    if (qs)
        url += `?${qs}`

    fetch(url, {
        method: 'GET',
        headers: {
            'content-type': 'application/json',
            'Authorization': ENV.Authorization,
            'x-auth-token': ENV.x_auth_token
        }
    }).then(res => res.json()).then(data => {
        if (data.success) {
            dispatch({
                type: GET_HISTORY,
                payload: data.data
            })
        } else {
            dispatch({
                type: GET_ERRORS,
                payload: data
            })
        }
    }).catch(error => {
        dispatch({
            type: GET_ERRORS,
            payload: error
        })
    })
};