import React, { useState, useEffect } from "react";
import { Modal } from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import Trending_slide from "../Trending_slide/Trending_slide";
import './Cart.css'
import { getBalance } from '../../utils/web3';
import { getNFTs, getNFT, nftPurchasing, setQuantities, beforeNFT } from '../ArtistInterface/Upload_create_modal/nft.action'
import { getRecentNfts } from "../NFT_Slider/recentNFTs.action";
import { getTrendingNfts } from "../Trending/trendingNFTs.action";
import { connect } from "react-redux";
import { ENV } from "../../config/config";
import { useHistory } from "react-router-dom";
import { mintNFTs } from '../../utils/web3'
import FullPageLoader from "../FullPageLoader/FullPageLoader";

function CartPopup({ nftType, hideModel, smShows, setBulkBuyShow, nft, ...props }) {
    // const [lgShow, setLgShow] = useState(false);

    // const hideModel = () => { setLgShow(false) }
    const [loader, setLoader] = useState(false)
    const history = useHistory();
    const user = ENV.getUserKeys('_id');
    const [WalletBalance, setWalletBalance] = useState(0);
    const payload = {
        nftId: nft?._id,
        prevOwner: nft?.owner?._id
    };
    const price = nft?.original ? (nft?.currentPrice * props?.nfts?.quantities?.artistCopies) : nft?.currentPrice;

    useEffect(() => {
        fetchWalletBalance();
    }, [])

    useEffect(() => {
        if (props.nfts.purchasingNFTAuth) {
            setLoader(false)
            hideModel();
            props.setQuantities({
                freeCopies: 0,
                artistCopies: 0,
                // sellerCopies: 0,
            });
            if (nftType === "recent")
                props.getRecentNfts();
            else if (nftType === "trending")
                props.getTrendingNfts();
            else if (nftType === "regular")
                props.getNFTs();
            else if (nftType === "single") {
                props.getNFT(nft?._id);
                return null;
            }
            props.beforeNFT();
            // setPrices({
            //     artistCopies: 0
            // });
        }
    }, [props.nfts.purchasingNFTAuth])

    useEffect(() => {
        if (props.error) {
            props.setQuantities({
                freeCopies: 0,
                artistCopies: 0,
                // sellerCopies: 0,
            });
            // setPrices({
            //     artistCopies: 0
            // });
            hideModel();
        }
    }, [props.error])

    const fetchWalletBalance = async () => {
        let balance = await getBalance();
        setWalletBalance(balance);
    }

    const handleNftPurchasing = async () => {
        if (!Object.keys(user).length)
            history.push("/login")
        else {
            setLoader(true)
            console.log("props.nftsprops.nftsprops.nftsprops.nfts");
            console.log(props.nfts);
            console.log(nft);
            if (nft?.original) {
                let mintNFTsRes = await mintNFTs({ metaData: nft.metaData, creator: nft.creator.address, freeTypes: props.nfts.quantities.freeCopies, paidTypes: props.nfts.quantities.artistCopies, tokenId: nft.tokenId }, price);
                console.log('CREATE PROJECT RESPONSE', mintNFTsRes);
                if (mintNFTsRes) {
                    let mintData = {
                        nftId: nft._id,
                        tokenIds: mintNFTsRes.tokenIds,
                        txHash: mintNFTsRes.txHash
                    }

                    const values = { ...payload };
                    // if (nft?.original) {
                    values.freeCopies = props.nfts.quantities.freeCopies;
                    values.artistCopies = props.nfts.quantities.artistCopies;
                    values.amount = price;
                    values.nftCopy = JSON.stringify(nft);
                    values.mintData = window.btoa(JSON.stringify(mintData))
                    values.txHash = mintData.txHash
                    // }
                    // else {
                    //     values.amount = nft?.currentPrice
                    // }

                    values.original = nft?.original;
                    values.status = nft?.status;
                    var formData = new FormData()
                    for (const key in values)
                        formData.append(key, values[key])
                    await props.nftPurchasing('nfts/nftPurchasing', formData)
                }
            }
            else {
                if (nft?.original) {
                    let buyNFTsRes = await buyNFT(nft.tokenId, nft.creator.address);
                    console.log('CREATE PROJECT RESPONSE', buyNFTsRes);
                    if (buyNFTsRes) {
                        const values = { ...payload };
                        values.amount = nft?.currentPrice

                        values.original = nft?.original;
                        values.status = nft?.status;
                        var formData = new FormData()
                        for (const key in values)
                            formData.append(key, values[key])
                        await props.nftPurchasing('nfts/nftPurchasing', formData)
                    }
                }
            }
        }
    }

    return (
        <>
            {loader && <FullPageLoader />}
            <Modal
                className="cart-popup bg-pink"
                show={smShows}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Body>
                    <div className="cart-content">
                        <div className="cross-btn float-end" onClick={() => {
                            hideModel()
                            props.setQuantities({
                                freeCopies: 0,
                                artistCopies: 0,
                                // sellerCopies: 0,
                            });
                        }}>
                            <FontAwesomeIcon icon={faTimes} />
                        </div>
                        <h2 className="sub-heading light-purple-color mb-4">Pay</h2>
                        <Trending_slide nft={nft} />
                        <div className="cart-details pt-3">
                            <div className="d-flex justify-content-between pt-4" >
                                <span className="price">Price</span>
                                <span className="ethereum-icon ethrum-rate d-flex"><span className="price-pay-span me-1">{price}</span> ETH</span>
                            </div>
                            <div className="d-flex justify-content-between pt-4" >
                                <span className="price">My wallet</span>
                                <span className="ethereum-icon ethrum-rate d-flex">
                                    <span className="price-pay-span me-1">{WalletBalance}</span>
                                </span>
                            </div>
                            {nft?.original && <div className="text-center">
                                <button type="button" className="btn-theme cart-button bg-gradient-theme" onClick={() => {
                                    hideModel();
                                    setBulkBuyShow(true);
                                }}>Back</button>
                            </div>}
                            <div className="text-center">
                                <button type="button" className="btn-theme cart-button ethereum-icon bg-gradient-theme">
                                    <span onClick={handleNftPurchasing}>Confirm &amp; pay</span>
                                </button>
                            </div>
                        </div>
                    </div>
                </Modal.Body>
            </Modal>
        </>
    )
}

const mapStateToProps = (state) => ({
    nfts: state.nfts,
    error: state.error,
})


export default connect(mapStateToProps, { nftPurchasing, setQuantities, getNFT, getNFTs, getRecentNfts, getTrendingNfts, beforeNFT })(CartPopup);